<template>
  <div>

    <van-swipe class="m-swipe" @change="onChange" :initial-swipe="currentShow" :show-indicators="false" :loop="false" v-on:scroll="onScroll1">
      <div class="custom-indicator" slot="indicator">
        <!--{{ current + 1 }}/4-->
        <comCalendarWeek ref="calendarWeekChild" @pickDate="reloadDatas"
                         :reDate="reDate" :currentYear="currentYear"
                         :currentMonth="currentMonth"
                         :currentDate="currentDate"
                         :currentShow="currentShow"
                         :flyDaysList="flyDaysList" style="border-bottom:1px solid #efefef"></comCalendarWeek>
      </div>
      <van-swipe-item class="m-swipe-item" v-for="n in weekDays" :key="n">
        <!--列表-->
        <!--<ul class="m-list">-->
        <!--<li v-for="(item,index) in dataList" :key="index" @click="jumpToSail(item,index)">-->
