        <!--<van-col span="16">-->
        <!--<van-row>-->
        <!--<van-col span="8">{{item.real_dept_time.substring(11,16)}}</van-col>-->
        <!--<van-col span="6">-->
        <!--<i class="iconfont icon-longLine m-list-long-icon"></i>-->
        <!--</van-col>-->
        <!--<van-col span="8">{{item.real_arr_time.substring(11,16)}}</van-col>-->
        <!--</van-row>-->
        <!--<van-row class="mt5">-->
        <!--<van-col span="8">{{item.arr_airport}}</van-col>-->
        <!--<van-col span="6">&nbsp;-->
