        <!--<van-col span="8">{{item.dept_airport}}</van-col>-->
        <!--</van-row>-->
        <!--</van-col>-->
        <!--<van-col span="8">-->
        <!--<span class="m-list-btn" v-if="index==0">即将起飞</span>-->
        <!--<span class="m-list-btn bg-blue" v-if="index==1">报告已生成</span>-->
        <!--<span class="m-list-btn bg-gray" v-if="index==2">报告生成中</span>-->
        <!--</van-col>-->
        <!--</van-row>-->
        <!--</div>-->
        <!--</li>-->
        <!--</ul>-->
        <ul class="m-list">
          <li v-for="(item,index) in flightListData.items[currentShow].flights" :key="index" @click="jumpToSail(item,index)">
            <div class="m-list-title">
              <span class="f-no">{{item.hangbanhao}}</span>
              <span class="f-type">{{item.duanjixing}}&nbsp;&nbsp;{{item.feijihao}}</span>
            </div>
            <div class="m-list-content">
              <van-row>
                <van-col span="16">
                  <van-row>
                    <van-col span="8">{{item.shijiqifei}}</van-col>
                    <van-col span="6">
                      <i class="iconfont icon-l-arrow m-list-long-icon"></i>
                    </van-col>
                    <van-col span="8">{{item.shijidaoda}}</van-col>
                  </van-row>
                  <van-row class="mt5">
                    <van-col span="8">{{item.qifeijichang}}</van-col>
                    <van-col span="6">&nbsp;
                    </van-col>
                    <van-col span="8">{{item.jiangluojichang}}</van-col>
                  </van-row>
                </van-col>
                <van-col span="8">
                  <span class="m-list-btn" v-if="item.zhuangtai==1">即将起飞</span>
                  <span class="m-list-btn bg-blue" v-if="item.zhuangtai==3">报告已生成</span>
                  <span class="m-list-btn bg-gray" v-if="item.zhuangtai==2">报告生成中</span>
