<style lang="scss" scoped>
  .m-swipe {
    margin-top: 10px;
    padding-top: 10px;
    position: relative;
    /*border-top:1px solid #efefef;*/
    .custom-indicator {
      height: 80px;
      position: absolute;
      top: 0px;
      width: 100%;
      text-align: center;
      color: #ff7a51;
      z-index: 1;
    }
    .m-swipe-item{
      margin-top: 80px;
      box-sizing:border-box;
    }
  }
  .m-list {
    /*position: fixed;*/
    /*left: 10px;*/
    /*right: 10px;*/
    /*top: 140px;*/
    height: calc(100% - 140px);
    overflow-y: auto;
    padding-bottom: 40px;
  }

  .m-list > li {
    background: #fff;
    height: 100px;
    position: relative;
    padding: 10px;
    border-bottom: 1px solid #e5e5e5;
    .m-list-title {
      padding: 5px 10px 10px 10px;
      position: relative;
      i {
        font-size: 20px;
        color: #0096ff;
      }
      .f-no {
        font-family: LangDon;
        font-size: 18px;
        color: #666;
      }
      .f-type {
        font-size: 14px;
        margin-left: 5px;
        color: #b5b5b5;
      }
    }
    .m-list-content {
      text-align: center;
      font-size: 14px;
      color: #333;
      * {
        text-align: center;
      }
      .m-list-btn {
        width: 86px;
        border-radius: 12px;
        background: #28c88c;
        color: #fff;
        text-align: center;
        font-size: 14px;
        line-height: 24px;
        display: inline-block;
      }
      .m-list-btn.bg-gray {
        background: #c8c8c8;
      }

      .m-list-btn.bg-blue {
        background: #7d92d3;
      }
      .m-list-long-icon {
        font-size: 50px;
        line-height: 20px;
      }
    }
  }
</style>

<script>
// import {Toast} from 'vant';
import * as mainApi from '../../../api/main';
import flightListData from './../homepagedata.json';
import comCalendarWeek from '../../../components/com_Calendar_week';
export default {
  props: {
  },
  ready () {
  },
  data () {
    return {
      current: 0,
      currentYear: 2019, // 年份
      currentMonth: 1, // 月份
      currentWeek: 1, // 星期
      userInfoData: [],
      flyDaysList: [], // 获取的日历信息
      showDate: false,
      weekDays: 7,
      currentShow: 1,
      currentDate: new Date(),
      reDate: '',
      flightListData: flightListData
    };
  },
  created () {
    this.currentShow = this.currentDate.getDay() - 1;
  },

  components: {
    comCalendarWeek: comCalendarWeek
  },
  methods: {

    loadData () {
      this.showLoading = true;
      var para = '?avrId=A00096&flightDate=2019-01-01';
      mainApi.getFlights.r(para).then(res => {
        if (res) {
          this.showLoading = false;
          // this.total = res.data.pageData.totalCount;
          this.dataList = res.data;
          for (let i in this.dataList) {
            var sDate = this.dataList[i].real_dept_time;
            var eDate = this.dataList[i].real_arr_time;
            this.dataList[i].flight_long = this.diffTime(sDate, eDate);
            if (this.dataList[i].task_id === '101080319') {
              this.dataList[i].starValue = 3;
            } else {
              this.dataList[i].starValue = 5;
            }
          }
          // this.listLoading = false;
        } else {
          this.showLoading = false;
          this.$message({
            message: '获取失败',
            type: 'error'
          });
        }
      }).catch(() => {
        // .catch((error)
        this.showLoading = false;
        //                 错误处理统一到utils/api.js 中通过拦截器调取utils/utils.js的catchError方法进行处理
      });
    },

    loadDatas () {
      this.showLoading = true;
      var para = '?avrId=A00096&flightDate=2019-01-01';
      mainApi.getFlights.r(para).then(res => {
        if (res) {
          this.showLoading = false;
          // this.total = res.data.pageData.totalCount;
          this.dataList = res.data;
          for (let i in this.dataList) {
            var sDate = this.dataList[i].real_dept_time;
            var eDate = this.dataList[i].real_arr_time;
            this.dataList[i].flight_long = this.diffTime(sDate, eDate);
            if (this.dataList[i].task_id === '101080319') {
              this.dataList[i].starValue = 3;
            } else {
              this.dataList[i].starValue = 5;
            }
          }
          // this.listLoading = false;
        } else {
          this.showLoading = false;
          this.$message({
            message: '获取失败',
            type: 'error'
          });
        }
      }).catch(() => {
        // .catch((error)
        this.showLoading = false;
        //                 错误处理统一到utils/api.js 中通过拦截器调取utils/utils.js的catchError方法进行处理
      });
    },

    reloadDatas (reDate) {
      if (reDate.getDay() === 0) {
        this.currentShow = 6;
      } else {
        this.currentShow = reDate.getDay() - 1;
      }
    },
    reloadData (reDate) {
      this.showLoading = true;
      var para = '?avrId=A00096&flightDate=2019-01-01';
      mainApi.getFlights.r(para).then(res => {
        if (res) {
          this.showLoading = false;
          // this.total = res.data.pageData.totalCount;
          this.dataList = res.data;
          for (let i in this.dataList) {
            var sDate = this.dataList[i].real_dept_time;
            var eDate = this.dataList[i].real_arr_time;
            this.dataList[i].flight_long = this.diffTime(sDate, eDate);
            if (this.dataList[i].task_id === '101080319') {
              this.dataList[i].starValue = 3;
            } else {
              this.dataList[i].starValue = 5;
            }
          }
          // this.listLoading = false;
        } else {
          this.showLoading = false;
          this.$message({
            message: '获取失败',
            type: 'error'
          });
        }
      }).catch(() => {
        // .catch((error)
        this.showLoading = false;
        //                 错误处理统一到utils/api.js 中通过拦截器调取utils/utils.js的catchError方法进行处理
      });
      // this.dataList = [
      //   {
      //     id: '1',
      //     fNo: 'CA1886',
      //     fType: 'B1415(B737)',
      //     starValue: 3,
      //     status: '未飞',
      //     sTime: '17:50',
      //     eTime: '20:00',
      //     fDate: '2018-11-02',
      //     sAirport: 'ZSSS虹桥',
      //     eAirport: 'ZGSZ宝安',
      //     fHour: '2h 25m',
      //     sBridge: '靠桥236',
      //     eBridge: '靠桥236'
      //   },
      // ];
    },
    jumpToSail (item, index) {
      // this.$store.commit('setTransition', 'turn-on');
      if (index === 0) {
        this.$router.push({
          path: '/Main/SailFront',
          query: {
            flightItem: JSON.stringify(item),
            flightId: item.task_id,
            flight_no: item.hangbanhao,
            arr_airport: item.jiangluojichang,
            arr_airport_no: item.jiangluojichang4,
            dept_airport_no: item.qifeijichang4,
            dept_airport: item.qifeijichang,
            aircraft_no: item.duanjixing,
            aircraft_type: item.feijihao
          }
        });
      } else {
        this.$router.push({
          path: '/Main/SailAfter',
          query: {
            flightItem: JSON.stringify(item),
            flightId: item.task_id,
            flight_no: item.hangbanhao,
            arr_airport: item.jiangluojichang,
            arr_airport_no: item.jiangluojichang4,
            dept_airport_no: item.qifeijichang4,
            dept_airport: item.qifeijichang,
            aircraft_no: item.duanjixing,
            aircraft_type: item.feijihao
          }
        });
      }
    },
    onScroll1 () {
      console.log('onScroll1已经触发');
    },
    onChange (index) {
      // && this.currentShow !== 6) {
      if (index < this.currentShow) {
        // 往前翻
        if (this.currentShow !== 0) {
          var preDate = new Date(this.currentDate.getTime() - 24 * 60 * 60 * 1000); // 前一天
          this.currentShow = preDate.getDay() - 1;
          this.currentDate = preDate;

          // this.$refs.calendarWeekChild.dayPre();
          this.$refs.calendarWeekChild.currentShow = index;
        }
      } else {
        if (this.currentShow !== 6) {
          // 往后翻
          // this.$refs.calendarWeekChild.dayNext();
          var nextDate = new Date(this.currentDate.getTime() + 24 * 60 * 60 * 1000); // 后一天
          if (nextDate.getDay() === 0) {
            this.currentShow = 6;
          } else {
            this.currentShow = nextDate.getDay() - 1;
          }
          this.currentDate = nextDate;

          this.$refs.calendarWeekChild.currentShow = index;
        }
      }
      this.current = index;
      // Toast('当前 Swipe 索引：' + index + '****' + this.currentShow);
    }
  }
};
</script>



