import routeMap from '../views/mine/routeMap.vue';
import myBadge from '../views/mine/myBadge.vue';// 我的徽章
import myData from '../views/mine/myData.vue';// 个人资料
import serCenter from '../views/mine/serCenter.vue';// 客服中心
import myRanking from '../views/mine/myRanking.vue';// 我的排名
import setUp from '../views/mine/setUp.vue';// 设置
import feedBack from '../views/mine/feedBack.vue';// 意见反馈
import jdpm from '../views/mine/jdpm.vue';
import wdda from '../views/mine/wdda.vue';
import wdzz from '../views/mine/wdzz.vue';
import zhengzhao from '../views/mine/zhengzhao.vue';
import fxpzyb from '../views/mine/fxpzyb.vue';
import myinfo from '../views/mine/myinfo.vue';
import myset from '../views/mine/myset.vue';
import flyCalendar from '../views/mine/flyCalendar.vue';
import fuelLevel from '../views/mine/fuelLevel.vue';
// 首页
export default [
  {path: '/Mine/routeMap', component: routeMap, name: '/Mine/routeMap', alias: '航线地图'},
  {path: '/Mine/myBadge', component: myBadge, name: '/Mine/myBadge', alias: '我的徽章'},
  {path: '/Mine/myData', component: myData, name: '/Mine/myData', alias: '个人资料'},
  {path: '/Mine/serCenter', component: serCenter, name: '/Mine/serCenter', alias: '客服中心'},
  {path: '/Mine/myRanking', component: myRanking, name: '/Mine/myRanking', alias: '我的排名'},
  {path: '/Mine/setUp', component: setUp, name: '/Mine/setUp', alias: '设置'},
  {path: '/Mine/feedBack', component: feedBack, name: '/Mine/feedBack', alias: '意见反馈'},
  {path: '/Mine/jdpm', component: jdpm, name: '/Mine/jdpm', alias: '机队排名'},
  {path: '/Mine/wdda', component: wdda, name: '/Mine/wdda', alias: '我的档案'},
  {path: '/Mine/wdzz', component: wdzz, name: '/Mine/wdzz', alias: '我的资质'},
  {path: '/Mine/zhengzhao', component: zhengzhao, name: '/Mine/zhengzhao', alias: '我的证照'},
  {path: '/Mine/fxpzyb', component: fxpzyb, name: '/Mine/fxpzyb', alias: '飞行品质月报'},
  {path: '/Mine/myinfo', component: myinfo, name: '/Mine/myinfo', alias: '我的消息'},
  {path: '/Mine/myset', component: myset, name: '/Mine/myset', alias: '我的设置'},
  {path: '/Mine/flyCalendar', component: flyCalendar, name: '/Mine/flyCalendar', alias: '飞行日历'},
  {path: '/Mine/fuelLevel', component: fuelLevel, name: '/Mine/fuelLevel', alias: '我的节油等级'}
];



