<template>
  <section>
    <v-touch v-on:swiperight="onSwipeRight" class="touchWapper" :swipe-options="{direction: 'horizontal'}">
    <!--头部组件-->
    <com_header :headerTitle="headerTitle"></com_header>
    <div class="c-container">
      <van-cell-group class="info-list">
        <van-cell v-for="(item,index) in dataList" :title="item.title" :key="index" class="info-list-cell">
          <template slot="title">
            <span class="title">{{item.title}}</span>
            <p class="font-gray">2019-01-01</p>
          </template>
          <template slot="icon">
            <i class="iconfont icon-shijian info-list-icon"></i>
          </template>
          <template>
            <span class="value">{{item.value}}</span>
          </template>
        </van-cell>
      </van-cell-group>
      <ul class="da-timeline">
        <li class="da-step">
          <div class="da-step-icon da-step-icon-gray"></div>
          <div class="da-step-line da-step-line-gray"></div>
        </li>
        <li class="da-step" v-for="(item,index) in timelineList" :key="index">
          <div class="da-step-line"></div>
          <div class="da-step-icon">
            <div class="da-step-title">{{item.title}}</div>
            <div class="da-step-date">{{item.sdate}}</div>
          </div>
        </li>
      </ul>
    </div>
    </v-touch>
  </section>

</template>
<style scoped lang="scss">
  .info-list {
    margin-top: 10px;
    .info-list-cell {
      padding: 15px;
      .info-list-icon {
        font-size: 16px;
        margin-right: 5px;
        color: #0096ff;
        font-weight: bold;
      }
      .title {
        color: #444;
        font-size: 14px;
      }
      .value {
        color: #0096ff;
        /*font-weight: bold;*/
        font-size: 24px;
        font-family: LangDon;
      }
    }
  }

  .da-timeline {
    display: flex;
    height: 100%;
    flex-flow: column;
    padding: 10px 5px;
    .da-step {
      position: relative;
      display: flex;
      flex-shrink: 1;
      height: 80px;
      //height: 100px;
      .da-step-icon {
        position: absolute;
        left:40%;
        background: #fff;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        border: 4px solid #0096ff;
        z-index: 1;
      }
      .da-step-line {
        position: absolute;
        border-color: inherit;
        background-color: #0096ff;
        width: 2px;
        top: 0;
        bottom: 0;
        left: 42.5%;
      }
      .da-step-icon-gray {
        border: 4px solid #ccc;
      }
      .da-step-line-gray {
        background-color: #ccc;
      }
    }
    .da-step-title {
      position: absolute;
      left: 30px;
      top: -6px;
      width: 120px;
      height: 32px;
      line-height: 32px;
      font-size: 14px;
      color: #fff;
      background: #0096ff;
      border-radius: 4px;
      padding:0 5px;
      box-shadow: 0px 0px 10px #ccc ;
    }
    .da-step-date {
      position: absolute;
      right: 32px;
      top: -6px;
      width: 100px;
      height: 32px;
      line-height: 32px;
      font-size: 14px;
      text-align: center;
      color: #0096ff;
      background: #fff;
      border-radius: 4px;
      padding:0 5px;
      box-shadow: 0px 0px 19px #ccc ;
    }
    .da-step-title:before,.da-step-title:after{
      content:"";
      display:block;
      border-width:8px;
      position:absolute;
      left:-15px;
      top:6px;
      border-style:dashed solid  dashed ;
      border-color: transparent  #09F transparent  transparent ;
      font-size:0;
      line-height:0;
    }
    .da-step-title:after{
      left:-14px;
    }
    .da-step-date:before,.da-step-date:after{
      content:"";display:block;
      border-width:8px;
      position:absolute;
      right:-15px;
      top:6px;
      border-style:solid dashed dashed;
      border-color: transparent  transparent transparent  #fff;
      font-size:0;
      line-height:0;
    }
    .da-step-date:after{
      right:-14px;
    }
  }

</style>
<script>
// 使用jquery
// import * as indexManageApi from '../../api/main';
import comHeader from '../components/header';

export default {
  // inject: ['reload'],
  data () {
    return {
      headerTitle: '我的档案',
      dataList: [],
      timelineList: []
    };
  },
  components: {
    com_header: comHeader
  },
  created () {
    this.loadData();
  },

  methods: {
    loadData () {
      this.dataList = [
        {id: 1, title: '总飞行经历时间 ', value: '120828.86'},
        {id: 2, title: '总起落架次 ', value: '1821'},
        {id: 3, title: '本机型总经历时间 ', value: '120828.86'}];
      this.timelineList = [
        {id: '1', isActive: true, title: '晋升 A1 机长', sdate: '2017-01-01'},
        {id: '2', isActive: false, title: '转机型 B737', sdate: '2016-01-01'},
        {id: '3', isActive: false, title: '晋升 J1 机长', sdate: '2015-01-01'}];
    }
  },
  mounted () {
  }
};

</script>



