<template>
  <div :id="pieChartOption.chartId" style=" height: 280px" :style="{width:pieChartOption.chartWidth}"></div>
</template>
<style scoped lang="scss">

</style>
<script>
import echarts from 'echarts';

export default {
  props: {
    pieChartOption: ''
  },
  data () {
    return {};
  },
  created () {
  },
  methods: {
    drawPie () {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById(this.pieChartOption.chartId));
      // 绘制图表
      myChart.setOption({
        // title: {
        //     text: '***统计',
        //     left: 'center',
        // },
        tooltip: {
          trigger: 'item',
          // formatter: "{a} <br/>{b} : {c} ({d}%)"
          formatter: '{b} : {c} ({d}%)'
        },
        // legend: {
        //     // orient: 'vertical',
        //     // top: 'middle',
        //     bottom: 10,
        //     left: 'center',
        //     data: ['1月', '2月','3月','4月','5月']
        // },
        color: this.pieChartOption.itemColor,
        series: [
          {
            type: 'pie',
            radius: '65%',
            center: ['50%', '50%'],
            selectedMode: 'single',
            data: this.pieChartOption.series_Data,
            //     [
            //     {value:1548, name: '1月'},
            //     {value:535, name: '2月'},
            //     {value:510, name: '3月'},
            //     {value:634, name: '4月'},
            //     {value:735, name: '5月'}
            // ],
            itemStyle: {
              normal: {
                label: {
                  // formatter: function (params) {
                  //   return params.name + ' ' + params.value + '%\n';
                  // },
                  formatter: function (params) {
                    return params.value;
                  },
                  textStyle: {
                    color: '#fff' // 改变标示文字的颜色
                  }
                },
                borderWidth: 2,
                borderColor: '#4f5064',
                labelLine: {
                  length: 5, // 改变标示线的长度
                  lineStyle: {
                    color: '#fff' // 改变标示线的颜色
                  }
                }
              },

              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      });
    }
  },
  watch: {},
  mounted () {
    // this.$nextTick(function () {
    this.drawPie();
    // });
  }
};
</script>



