  methods: {

    loadData () {
      var para = '?avrId=' + this.userInfoData.avrId;
      mineApi.getF1igthGrade.r(para).then(res => {
        if (res) {
          this.dataList = res.data;
        } else {
          this.$message({
            message: '获取失败',
            type: 'error'
          });
        }
      }).catch(() => {
        // .catch((error)
        //   this.listLoading = false;
        //                 错误处理统一到utils/api.js 中通过拦截器调取utils/utils.js的catchError方法进行处理
      });
    },
    openPage (to) {
      this.$router.push({path: to});
    }
  },
  mounted () {
  }
};

</script>



