<template>
  <section>
      <!--<v-touch v-on:swiperight="onSwipeRight" class="touchWapper" :swipe-options="{direction: 'horizontal'}">-->
    <!--头部组件-->
    <com_header :headerTitle="headerTitle"></com_header>
    <div class="sa-content">
      <div class="s-c" v-for="(item,index) in flightData" :key="index">
        <v-touch v-if="item.moduleId != 'czjyzn'" v-on:swiperight="onSwipeRight(item.moduleId)"
                 :swipe-options="{direction: 'horizontal'}">
          <div class="s-title">
            <i class="iconfont" :class="item.moduleIcon"></i> {{item.moduleName}}
            <!--跳转到我的数据-->
            <span class="s-title-r" v-if="item.moduleId=='jyyb'" @click="jump('/Main/MyData')">我的数据<i
              class="iconfont icon-jiantou1"></i></span>
          </div>
          <!--航班信息-->
          <com_hbxx :baseData="item.baseData" :desData="item.desData" v-if="item.moduleId=='hbxx'"></com_hbxx>
          <!--节油预报-->
          <com_jyyb :baseData="item.baseData" v-if="item.moduleId=='jyyb'"></com_jyyb>
          <!--&lt;!&ndash;操作节油指南&ndash;&gt;-->
          <!--<com_czjyzn :baseData="item.baseData" :desData="item.desData"  v-if="item.moduleId=='czjyzn'"></com_czjyzn>-->
          <!--航班油耗预报-->
          <com_hbyhyb :baseData="item.baseData" :desData="item.desData" v-if="item.moduleId=='hbyhyb'"></com_hbyhyb>
          <!--运行质量考核项目预报-->
          <com_yxzlkhxmyb :baseData="item.baseData" :desData="item.desData"
                          v-if="item.moduleId=='yxzlkhxmyb'"></com_yxzlkhxmyb>
        </v-touch>
        <div v-else>
          <div class="s-title">
            <i class="iconfont" :class="item.moduleIcon"></i> {{item.moduleName}}
            <!--跳转到地图-->
            <span class="s-title-r" v-if="item.moduleId=='czjyzn'" @click="jump('/Mine/routeMap')">地图<i
              class="iconfont icon-jiantou1"></i></span>
          </div>
          <!--操作节油指南-->
          <com_czjyzn :baseData="item.baseData" :desData="item.desData"  :guideType="guideType" v-if="item.moduleId=='czjyzn'"></com_czjyzn>
        </div>
      </div>
    </div>
    <!--</v-touch>-->
  </section>
</template>
<style scoped lang="scss">
  .sa-content {
    /*width: 100%;*/
    /*min-height: calc(100% - 44px);*/
    height: 100%;
    width: 100%;
    overflow-y: auto;
    padding-top: 39px;
    box-sizing: border-box;
    background: #f2f2f2;
    margin-top: 10px;
    -webkit-overflow-scrolling:touch;
  }
  .s-c{
    padding:10px 15px;
    background: #fff;
    box-sizing: border-box;
    border-bottom: 1px solid #e5e5e5;
    .s-title{
      line-height: 30px;
      margin-bottom: 20px;
      font-size: 16px;
      box-sizing: border-box;
      position: relative;
      i{
        color: #2aca8e;
        margin-right: 5px;
        font-size: 20px;
      }
    }
    .s-title-r{
      position: absolute;
      right: 0;
      line-height: 30px;
      font-size: 14px;
      color: #999;
      i{
        font-size: 14px;
        color: #999;
      }
    }
  }
</style>
<script>
// import * as indexManageApi from '../../api/main';
import comHeader from '../components/header';
import hbxx from './components/hbxx';
import jyyb from './components/jyyb';
import czjyzn from './components/czjyzn';
import hbyhyb from './components/hbyhyb';
import yxzlkhxmyb from './components/yxzlkhxmyb';
export default {
  data () {
    return {
      // clientHeight: document.body.clientHeight,
      headerTitle: '航前节油指南',
      dataList: [],
      listLoading: false,
      guideType: 'hangqian',
      keyWord: '',
      stepList: [],
      flightData: [],
      flightItem: JSON.parse(this.$route.query.flightItem),
      flightId: this.$route.query.flightId,
      flight_no: this.$route.query.flight_no,
      arrAirport: this.$route.query.arr_airport,
      deptAirport: this.$route.query.dept_airport,
      arrAirportNo: this.$route.query.arr_airport_no,
      deptAirportNo: this.$route.query.dept_airport_no,
      aircraftNo: this.$route.query.aircraft_no,
      aircraftType: this.$route.query.aircraft_type
    };
  },
  created () {
    this.loadData();
  },
  components: {
    com_header: comHeader,
    com_hbxx: hbxx,
    com_jyyb: jyyb,
    com_czjyzn: czjyzn,
    com_hbyhyb: hbyhyb,
    com_yxzlkhxmyb: yxzlkhxmyb
  },
  methods: {
    onClick (index, title) {
      // this.$toast(title);
    },

    loadData () {
      this.flightData = [
        {
          moduleId: 'hbxx',
          moduleName: '航班信息',
          moduleIcon: 'icon-fly-info',
          baseData: {
            flight_no: this.flight_no,
            aircraftType: this.aircraftType,
            aircraftNo: this.aircraftNo,
            deptAirportNo: this.deptAirportNo,
            arrAirportNo: this.arrAirportNo,
            deptAirport: this.deptAirport,
            arrAirport: this.arrAirport
          },
          desData: [
            {desName: '计划起飞', desValue: this.flightItem.jihuaqifei},
            {desName: '计划到达', desValue: this.flightItem.jihuadaoda},
            {desName: '预计起飞', desValue: this.flightItem.yujiqifei},
            {desName: '预计到达', desValue: this.flightItem.yujidaoda},
            // {desName: '实际起飞', desValue: this.flightItem.shijiqifei},
            // {desName: '实际到达', desValue: this.flightItem.shijidaoda},
            {desName: '停机位', desValue: this.flightItem.chugangkaoqiao},
            {desName: '停机位', desValue: this.flightItem.jingangkaoqiao},
            {desName: '计划过站', desValue: this.flightItem.guozhanshijianjihua},
            {desName: '实际过站', desValue: this.flightItem.guozhanshijianshiji}]
        },
        {moduleId: 'jyyb', moduleName: '节油预报', moduleIcon: 'icon-youliang', baseData: {}, desData: []},
        {moduleId: 'czjyzn', moduleName: '操作节油指南', moduleIcon: 'icon-zhinan', baseData: {}, desData: []},
        {moduleId: 'hbyhyb',
          moduleName: '航班油耗预报',
          moduleIcon: 'icon-youliang',
          baseData: {fuelNum: '5878Kg'},
          desData: [
            {desValue1: 'APD ' + this.flightItem.APD, desValue2: 'APD ' + this.flightItem.jizhunAPD},
            {desValue1: '业载 ' + this.flightItem.yezai, desValue2: '业载 ' + this.flightItem.jizhunyezai},
            {desValue1: '距离 ' + this.flightItem.xunhanggaodu, desValue2: '距离 ' + this.flightItem.jizhunxunhanggaodu}
          ]},
        {moduleId: 'yxzlkhxmyb',
          moduleName: '运行质量考核项目预报',
          moduleIcon: 'icon-youliang',
          baseData: {},
          desData: [
            {desValue1: '巡航高度层 ', desValue2: '30100ft '},
            {desValue1: 'CI执行率 ', desValue2: '70% '},
            {desValue1: '过站时间 ', desValue2: ''},
            {desValue1: '建议本次航班提前 ', desValue2: '10min关舱门 '},
            {desValue1: '历史平均过站时间 ', desValue2: '35min'},
            {desValue1: '航班正点 ', desValue2: ''},
            {desValue1: '建议航班实际到达时间小于 ', desValue2: ''}
          ]}
      ];
    },
    jump (url) {
      this.$router.push({
        path: url,
        query: {
        }
      });
    }
  }
};

</script>



