<template>
  <section>
    <div class="header-bg"></div>
    <!--头部组件-->
    <com_header :headerTitle="headerTitle"></com_header>
    <div class="c-container">
      <van-row type="flex" justify="space-between" class="e-top-row">
        <van-col span="16">2018-07-01 -- 2018-08-01</van-col>
        <van-col span="8" class="text-right"><span class="e-num">1029</span> 个航班</van-col>
      </van-row>
      <div class="e-card-box">
        <ul class="e-card-row">
          <div class="ling-bg"></div>
          <li :class="ecardActive==1?'is-active':''" @click="openRadar(1)">
            <div class="icon-with-line">
              <i class="iconfont icon-weibiaoti--20  line-icon"></i>
            </div>
            <span>ZSSS</span>
          </li>
          <li :class="ecardActive==2?'is-active':''" @click="openRadar(2)">
            <div class="icon-with-line">
              <i class="iconfont icon-weibiaoti--19  line-icon"></i>
            </div>
            <span>起飞</span>
          </li>
          <li :class="ecardActive==3?'is-active':''" @click="openRadar(3)">
            <div class="icon-with-line">
              <i class="iconfont icon-weibiaoti--21  line-icon"></i>
            </div>
            <span>巡航</span>
          </li>
          <li :class="ecardActive==4?'is-active':''" @click="openRadar(4)">
            <div class="icon-with-line">
              <i class="iconfont icon-weibiaoti--18  line-icon"></i>
            </div>
            <span>进近</span>
          </li>
          <li :class="ecardActive==5?'is-active':''" @click="openRadar(5)">
            <div class="icon-with-line">
              <div class="xie-line  xie-line5"></div>
              <i class="iconfont icon-weibiaoti--22  line-icon"></i>
            </div>
            <span>ZGSZ</span>
          </li>
        </ul>
      </div>

      <div class="e-card-box" style="height: 400px">
        <chartRadar ref="childRadar" :radarChartOption="radarChartOption"></chartRadar>
      </div>
    </div>
  </section>

</template>
<style scoped lang="scss">
  .e-top-row {
    color: #fff;
    font-size: 14px;
    line-height: 30px;
    padding: 0 15px;
  }

  .e-num {
    font-size: 20px;
    font-family: LangDon;
  }

  .e-card-box {
    margin: 15px;
    background: #fff;
    height: 110px;
    border-radius: 3px;
    position: relative;
    padding: 5px 10px;
    box-shadow: 0px 0px 10px #ccc;
    text-align: center;
  }

  .e-card-row {
    width: 100%;
    height: 100%;
    position: relative;
    .ling-bg {
      position: absolute;
      top: 0px;
      left: 0;
      width: 100%;
      height: 70px;
      content: '';
      background: url("../../assets/images/line-bg.png");
      background-size: 100% 100%;
    }
    li.is-active {
      color: #0096ff;
      i, span {
        color: #0096ff;
      }
    }
    li {
      float: left;
      font-size: 14px;
      color: #444;
      text-align: center;
      i {
        font-size: 24px;
        color: #999
      }
      span {
        font-weight: bold;
        line-height: 24px;
      }
      .icon-with-line {
        position: relative;
        height: 80px;
        width: 100%;
        text-align: center;
      }
    }
    li:nth-child(6n+2) {
      width: 18%;
      .line-icon {
        position: absolute;
        bottom: 8px;
        left: 5px;
      }
    }
    li:nth-child(6n+3) {
      width: 18%;
      .line-icon {
        position: absolute;
        bottom: 25px;
        left: 10px;
      }
    }
    li:nth-child(6n+4) {
      width: 28%;
      .line-icon {
        position: absolute;
        bottom: 56px;
        left: 35px;
      }
    }
    li:nth-child(6n+5) {
      width: 18%;
      .line-icon {
        position: absolute;
        bottom: 25px;
      }
    }
    li:nth-child(6n+6) {
      width: 18%;
      .line-icon {
        position: absolute;
        bottom: 8px;
      }
    }

  }
</style>
<script>
// 使用jquery
// import * as indexManageApi from '../../api/main';
import comHeader from '../components/header';
import chartRadar from '../components/charts/chart_radar';

export default {
  // inject: ['reload'],
  data () {
    return {
      headerTitle: '飞行品质月报',
      dataList: [],
      ecardActive: 2,
      radarChartOption: [],
      indicatorData: [],
      seriesData: []
    };
  },
  components: {
    com_header: comHeader,
    chartRadar: chartRadar
  },
  created () {
    this.loadData();
    this.indicatorData = this.dataList[1].dataArr;
    this.seriesData = this.dataList[1].seriesData;
    this.drawRadar();
  },

  methods: {

    loadData () {
      this.dataList = [
        {
          id: 1,
          name: '起飞机场',
          dataArr: [
            {name: '直线滑行最\n大地速\n11.5 vs 11.8', max: 20},
            {name: '转弯滑行最\n大地速\n7.6 vs 7.2', max: 20},
            {name: '进跑道转弯\n地速\n11 vs 9.9', max: 20},
            {name: '进停机位最\n大速度\n4 vs 5', max: 20}
          ],
          seriesData: [
            {
              value: [11.2, 7.5, 11.8, 5],
              name: '机队'
            },
            {
              value: [11.6, 7.1, 9, 4],
              name: '个人'
            }]
        },
        {
          id: 2,
          name: '起飞阶段',
          dataArr: [
            {name: '起飞最大俯\n仰姿态\n12 vs 9.8', max: 20},
            {name: '起飞平均抬\n轮率\n0.26 vs 0.21', max: 1},
            {name: '离地时刻俯\n仰姿态\n8 vs 7', max: 10},
            {name: '起飞地速\n104 vs 105', max: 150},
            {name: '起飞最大抬\n轮率\n2.8 vs 3.2', max: 10},
            {name: '起飞最大滚\n转坡度\n5 vs 6.2', max: 10}
          ],
          seriesData: [
            {
              value: [10, 0.5, 7, 104, 2, 4],
              name: '机队'
            },
            {
              value: [12, 0.2, 8, 105, 3, 6],
              name: '个人'
            }]
        },
        {
          id: 3,
          name: '巡航阶段',
          dataArr: [
            {name: '巡航最大表速\n289 vs 291', max: 1000},
            {name: '巡航最大马\n赫数\n0.726 vs 0.751', max: 1},
            {name: '巡航最大真\n空速\n468 vs 471', max: 1000},
            {name: '空中最大垂\n直载荷\n1.4 vs 1.5kt', max: 5}
          ],
          seriesData: [
            {
              value: [281, 0.75, 468, 1.4],
              name: '机队'
            },
            {
              value: [290, 0.9, 471, 1.5],
              name: '个人'
            }]
        },
        {
          id: 4,
          name: '进近阶段',
          dataArr: [
            {name: '50英尺到接\n地距离\n512 vs 526', max: 1000},
            {name: '50ft至接地\n最大下降率\n0.26 vs 0.21', max: 1},
            {name: '100ft以下\n最大坡度\n3.74 vs 4.21', max: 10},
            {name: '50英尺至接\n地时长\n7.2 vs 7.9', max: 10},
            {name: '起50ft至接地\n最大垂直载荷\n1.39 vs 1.32', max: 10},
            {name: '0-35英尺最\n大坡度\n0.53 vs 0.62', max: 1}
          ],
          seriesData: [
            {
              value: [531, 0.25, 3, 8, 1.4],
              name: '机队'
            },
            {
              value: [512, 0.29, 4, 7, 1.5],
              name: '个人'
            }]
        },
        {
          id: 5,
          name: '目的机场',
          dataArr: [
            {name: '解除反推时\n候的地速\n61.2 vs 60.8', max: 100},
            {name: '着陆使用反\n推时长\n18 vs 18', max: 20},
            {name: '着陆滑跑时\n长28 vs 27', max: 40},
            {name: '脱离跑道时\n的地速\n14.6 vs 13.9', max: 20},
            {name: '直线滑行最\n大地速\n11.5 vs 11.8', max: 20},
            {name: '转弯滑行最\n大地速\n7.6 vs 7.2', max: 20}
          ],
          seriesData: [
            {
              value: [60, 18, 26, 14, 11, 7],
              name: '机队'
            },
            {
              value: [61.8, 18, 28, 13, 12, 9],
              name: '个人'
            }]
        }
      ];
    },
    drawRadar () {
      this.radarChartOption = {
        chartId: 'radarChart1',
        chartWidth: '100%',
        itemColor: ['#0096ff', '#ff0000'],
        // xAxis_Data: ["1月","2月","3月","4月","5月","6月"],
        indicatorData: this.indicatorData,
        series_Data: this.seriesData,
        chartLoading: false
      };
    },
    openRadar (val) {
      this.ecardActive = val;
      for (let i in this.dataList) {
        if ((parseInt(i) + 1) === this.ecardActive) {
          this.indicatorData = this.dataList[i].dataArr;
          this.seriesData = this.dataList[i].seriesData;
        }
      }
      this.$nextTick(function () {
        this.drawRadar();
        this.$refs.childRadar.drawRadar(this.radarChartOption);
      });
    }
  },
  // watch: {
  //   //观察data的变化
  //   investmentnum: {
  //     handler (newVal, oldVal) {
  //       if (this.chart) {
  //         this.drawRadar();
  //       } else {
  //         this.init()
  //       }
  //     },
  //     deep: true //对象内部属性的监听，关键。
  //   },
  mounted () {
  }
};

</script>



