<!--suppress ALL -->
<template>
  <section>
    <!--头部组件-->
    <com_header :headerTitle="headerTitle"></com_header>
    <div class="u-box-wrap">
      <div class="u-box">
        <van-row>
          <van-col span="12">
            <p class="u-name">{{userInfoData.name}}</p>
            <p class="u-12">安全飞行时长</p>
            <p class="u-num">160</p>
          </van-col>
          <van-col span="12">
            <p class="u-date" @click="chooseTime" >
              {{ currentYear }} 年 {{ currentMonth }} 月
              <i class="iconfont icon-jiantou font12"> </i>
            </p>
            <p class="u-12">机队排名</p>
            <p class="u-num">121</p>
          </van-col>
        </van-row>
      </div>
    </div>
    <!--<Calendar style="background-color: red"-->
    <!--v-on:choseDay="clickDay"-->
    <!--v-on:changeMonth="changeDate"></Calendar>-->
    <comCalendar ref="calendarChild" :currentYear="currentYear"  :currentMonth="currentMonth" :flyDaysList="flyDaysList"></comCalendar>
    <van-popup v-model="showPopup" position="bottom" :overlay="true">
      <van-datetime-picker
        v-model="selectTime"
        type="year-month"
        :formatter="formatter"
        @change="changeDate"
        @confirm="sure"
        @cancel="cancelDate"
      />
    </van-popup>
  </section>
</template>
<style scoped lang="scss">
  .u-box-wrap {
    width: 100%;
    padding-top: 55px;
    padding-bottom: 15px;
  }

  .u-box {
    width: 80%;
    margin: 0 auto;
    height: 100px;
    font-size: 14px;
    border-radius: 5px;
    background: #fff url("../../assets/images/calendarbg.png");
    background-size: 100% 100%;
    padding: 10px 20px;
    line-height: 40px;
    .u-date{
      width: 110px;
      font-size: 14px;
      border-bottom: 1px solid #999 ;
      line-height: 30px;
      margin-bottom: 10px;
      padding-left: 5px;
    }
    .u-12 {
      font-size: 12px;
      line-height: 18px;
    }
    .u-name {
      font-size: 20px;
      color: #333;
      font-weight: bold;
    }
    .u-num {
      font-size: 32px;
      font-family: LangDon;
      color: #444;
    }
  }
</style>
<script>
// 使用jquery
// import Calendar from 'vue-calendar-component';
import comHeader from '../components/header';
// import comCalendar from '../../components/com_Calendar';
import comCalendar from '../../components/com_Calendar';
import * as sailAfterApi from '../../api/sailAfter';

export default {
  data () {
    return {
      headerTitle: '飞行日历',
      listLoading: false,
      keyWord: '',
      showPopup: false,
      selectTime: '',
      currentYear: 2018, // 年份
      currentMonth: 12, // 月份
      currentDay: 1, // 日期
      currentWeek: 1, // 星期
      userInfoData: [],
      flyDaysList: [] // 获取的日历信息
    };
  },
  created () {
    // this.userInfoData = JSON.parse(sessionStorage.getItem('userInfoData'));
    this.userInfoData = this.$store.getters.getStorage.user;
    var date = new Date();
    this.currentDay = date.getDate(); // 今日日期 几号
    this.currentYear = date.getFullYear(); // 当前年份
    this.currentMonth = date.getMonth() + 1; // 当前月份

    this.loadData();
  },
  components: {
    // Calendar: Calendar,
    com_header: comHeader,
    comCalendar: comCalendar
  },
  methods: {
    onValuesChange (picker, values) {
      if (values[0] > values[1]) {
        picker.setSlotValue(1, values[0]);
      }
    },
    loadData () {
      this.listLoading = true;
      var para = '?pilot=' + this.userInfoData.avrId + '&date=' + this.currentYear + '-' + this.currentMonth;
      // var para = '?pilot=' + this.userInfoData.avrId + '&date=2018-12';
      // console.log(this.userInfoData.avrId);
      sailAfterApi.flightdataJson.r(para).then(res => {
        if (res) {
          var dataList = res.data;
          for (let i in dataList) {
            var key = Object.keys(dataList[i])[0];
            var value = dataList[i][key];
            if (value.length > 0) {
              var isNormal = 'Y';
              if (value[0].status_value !== '正常') {
                isNormal = 'N';
              }
              var dateObj = {
                resttype: 'R',
                day: parseInt(i) + 1,
                isNormal: isNormal
              };
              this.flyDaysList.push(dateObj);
            }
          }
          this.$refs.calendarChild.dealResult(this.currentYear, this.currentMonth, this.flyDaysList);
        } else {
          this.$message({
            message: '获取失败',
            type: 'error'
          });
        }
        this.listLoading = false;
      }).catch(() => {
        this.listLoading = false;
      });
    },
    clickDay (data) {
      console.log(data); // 选中某天
    },
    changeDate (data) {
      console.log(data); // 左右点击切换月份
    },
    clickToday (data) {
      console.log(data); // 跳到了本月
    },

    //      确定时间
    sure () {
      this.showPopup = false;
      let selectYearMonth = this.dateFtt('yyyy-MM', this.selectTime);
      let timeArr = selectYearMonth.split('-');
      this.currentYear = timeArr[0];
      this.currentMonth = timeArr[1];
      this.loadData();
      // this.now_year = time[0];
      // this.now_month = parseInt(time[1]);
      // this.now_day = parseInt(time[2]);
    },
    cancelDate () {
      // alert('cancelDate');
      this.showPopup = false;
    },
    formatter (type, value) {
      if (type === 'year') {
        return `${value}年`;
      } else if (type === 'month') {
        return `${value}月`;
      }
      return value;
    },
    //      选择时间
    chooseTime () {
      this.showPopup = true;
    },
    dateFtt (fmt, date) {
      var o = {
        'M+': date.getMonth() + 1, // 月份
        'd+': date.getDate(), // 日
        'h+': date.getHours(), // 小时
        'm+': date.getMinutes(), // 分
        's+': date.getSeconds(), // 秒
        'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
        'S': date.getMilliseconds() // 毫秒
      };
      if (/(y+)/.test(fmt)) { fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length)); }
      for (var k in o) {
        if (new RegExp('(' + k + ')').test(fmt)) { fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length))); }
      }
      return fmt;
    }
  }
};

</script>



