import Home from '../views/home.vue';
import Main from '../views/main/Main.vue';
import addressBook from '../views/addressBook/addressBook.vue';
import FriendsCircle from '../views/FriendsCircle/FriendsCircle.vue';
import Mine from '../views/mine/Mine.vue';
// 首页
export default [
  {
    path: '/',
    component: Home,
    name: '',
    children: [
      {path: '/Main', component: Main, name: '/Main', alias: '首页'},
      {path: '/addressBook', component: addressBook, name: '/addressBook', alias: '通讯录'},
      {path: '/FriendsCircle', component: FriendsCircle, name: '/FriendsCircle', alias: '朋友圈'},
      {path: '/Mine', component: Mine, name: '/Mine', alias: '我的'}
    ]
  }
];



