<template>
  <section>
    <v-touch v-on:swiperight="onSwipeRight" class="touchWapper" :swipe-options="{direction: 'horizontal'}">
    <!--头部组件-->
    <com_header :headerTitle="headerTitle"></com_header>
    <div class="c-container">
      <van-cell-group class="my-info-list">
        <van-cell title="公司公告"  size="large" is-link>
          <template slot="title">
            <span  class="h-title">公司公告</span>
          </template>
        </van-cell>
        <van-cell title="航班信息资讯"  size="large" is-link>
          <template slot="title">
            <span  class="h-title">航班信息资讯</span>
          </template>
          <template>
            <van-tag round type="danger" class="mr5">2</van-tag>
          </template>
        </van-cell>
        <van-cell title="管理信息资讯"  size="large" is-link>
          <template slot="title">
            <span  class="h-title">管理信息资讯</span>
          </template>
        </van-cell>
      </van-cell-group>
    </div>
    </v-touch>
  </section>

</template>
<style scoped lang="scss">
  .my-info-list{
    margin-top: 10px;
      .h-title{
        font-size: 14px;
      }
  }
</style>
<script>
// 使用jquery
// import * as indexManageApi from '../../api/main';
import comHeader from '../components/header';

export default {
  // inject: ['reload'],
  data () {
    return {
      headerTitle: '消息',
      dataList: []
    };
  },
  components: {
    com_header: comHeader
  },
  created () {
    this.loadData();
  },

  methods: {
    loadData () {
      this.dataList = [
        {id: 1, title: '姓名 ', value: '卓易航'}];
    }
  },
  mounted () {
  }
};

</script>



