<template>
  <div class="header"  :class="hearderColor=='black'?'header-black':'header-white'">
    <!--<van-icon name="arrow-left" class="back-btn"  @click="backGo"/>-->
    <i class="iconfont icon-jiantouleft left-btn"  @click="backGo"></i>
    <!--<i class="mintui mintui-back back-btn" @click="backGo"></i>-->
    <span>{{headerTitle}}</span>
    <!--<van-icon name="ellipsis" class="more-btn"  @click="selMenu"/>-->
    <i class="iconfont icon-more2 right-btn"  @click="selMenu"></i>
    <van-popup v-model="popupVisible" position="bottom" :overlay="false">
      <div style="width: 100%; height: 50px;line-height:50px; color: #0096ff" @click="loginOut()">退出登录</div>
    </van-popup>
  </div>
</template>
<style scoped lang="scss">

</style>
<script>
export default {
  props: {
    headerTitle: {default: ''},
    hearderColor: {default: 'white'}
  },
  data () {
    return {
      popupVisible: false
    };
  },
  created () {
    this.loadData();
  },
  methods: {
    selMenu () {
      this.popupVisible = true;
    },
    loadData () {

    },
    backGo (to) {
      this.$router.back(-1);
    },
    jump (to) {
      if (this.$router) {
        this.$router.push(to);
      }
    },
    loginOut () {
      this.$store.commit('$_removeStorage');
      this.$router.push('/login');
    }
  },
  watch: {},
  mounted () {
    this.$nextTick(function () {
    });
  }
};
</script>



