<template>
  <div class="user-cover">
    <div class="featured" :style="{'background-image': getImage('cover')}">
    </div>
    <div class="user-info">
      <div class="nickname" v-text="FL"></div>
      <div class="avatar" :style="{'background-image': getImage('avatar')}"></div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    user: {
      type: Object
    }
  },
  data () {
    return {
      FL: '祖海亮'
    };
  },
  methods: {
    getImage (key) {
      var url;
      if (key === 'cover') {
        url = 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1550488446055&di=8997698f41a31c757b6ab9c26a754503&imgtype=0&src=http%3A%2F%2Fpic26.photophoto.cn%2F20130305%2F0014026709623105_b.jpg';
      } else if (key === 'avatar') {
        url = 'http://ww2.sinaimg.cn/large/7d6d3cf6jw1eh150a2766j20fs0fsjsm.jpg';
      }
      console.log(url);
      return `url(${url})`;
    }
  }
};
</script>
<style lang="scss" scoped>
  .user-cover {
    position: relative;
  }

  .featured {
    background-color: #C4C4C4;
    background-size: cover;
    height:240px;
    width: 100%;
  }

  .user-info {
    position: absolute;
    right: 10px;
    bottom: -15px;
    z-index: 10;

    > .nickname {
      font-weight: bold;
      font-size:14px;
      color: #FFFFFF;
      text-shadow: 0px 1px 0px rgba(0, 0, 0, 0.7);
      position: absolute;
      top: 5px;
      right: 80px;
      text-align: right;
      width: 100px;
    }
    > .avatar {
      width: 60px;
      height:  60px;
      border-radius: 5px;
      background-color: #87CEFA;
      background-size: cover;
      /*border: 2px solid #FFF;*/
      /*box-shadow: 0 0 0 1px #C7C7C7;*/
    }
  }

</style>



