<template>
  <section>
    <!--头部组件-->
    <com_header :headerTitle="headerTitle"></com_header>
    <div class="sa-top">
      <van-row>
        <van-col span="7" class="sa-name">{{flight_no}}</van-col>
        <van-col span="7">{{aircraftNo}}({{aircraftType}})</van-col>
        <van-col span="3" offset="1">{{deptAirport}}</van-col>
        <van-col span="3" class="sa-plane">
          <div class="sa-plane-bg"></div>
          <i class="iconfont icon-fj-2"></i></van-col>
        <van-col span="3">{{arrAirport}}</van-col>
      </van-row>
    </div>
    <div class="sa-content">
      <van-tabs type="card" class="sa-tabs" @click="onClick" color="#4f5064">
        <van-tab title="品质分析">
          <sa_kpi :flightId="flightId"></sa_kpi>
        </van-tab>
        <van-tab title="天气分析">
          <sa_weather :arrAirport="arrAirport" :deptAirport="deptAirport"></sa_weather>
        </van-tab>
        <van-tab title="事件分析">
          <sa_event :flightId="flightId"></sa_event>
        </van-tab>
      </van-tabs>
    </div>
  </section>
</template>
<style scoped lang="scss">

  .sa-top {
    position: fixed;
    top: 40px;
    width: 100%;
    background: #4f5064;
    font-size:14px;
    height: 40px;
    line-height: 40px;
    color: #fff;
    z-index: 999;
    .sa-name {
      padding-left: 10px;
      font-size: 16px;
    }
    .sa-plane {
      position: relative;
      text-align: center;
      .sa-plane-bg {
        position: absolute;
        top: 19px;
        width: 40px;
        height: 1px;
        border-top: 1px dashed #fff;
      }
    }
  }

  .sa-content {
    padding-top: 80px;
    width: 100%;
    min-height: calc(100% - 84px);
    background: #f2f2f2;
  }
</style>
<script>
// import * as indexManageApi from '../../api/main';
import comHeader from '../components/header';

export default {
  data () {
    return {
      headerTitle: '航后',
      dataList: [],
      listLoading: false,
      keyWord: '',
      stepList: [],
      flightId: this.$route.query.flightId,
      flight_no: this.$route.query.flight_no,
      arrAirport: this.$route.query.arr_airport,
      deptAirport: this.$route.query.dept_airport,
      aircraftNo: this.$route.query.aircraft_no,
      aircraftType: this.$route.query.aircraft_type
    };
  },
  created () {
    this.loadData();
  },
  components: {
    com_header: comHeader
  },
  methods: {
    onClick (index, title) {
      // this.$toast(title);
    },
    loadData () {
    }
  }
};

</script>



