<template>
  <div :id="barChartOption.chartId" style=" height: 280px;" :style="{width:barChartOption.chartW}" ></div>
</template>
<style scoped lang="scss">
</style>
<script>
import $ from 'jquery';
import echarts from 'echarts';

export default {
  props: {
    barChartOption: {
      default: {
        chartW: '100%'
      }
    }
  },
  data () {
    return {
      // chartW: '100%'
      // chartW: '50%'
    };
  },
  created () {
    // this.chartW = $(window).width();
    // this.barChartOption.chartW = '100%';
  },
  methods: {
    drawBar () {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById(this.barChartOption.chartId));
      // 绘制图表
      myChart.setOption({
        // title: {
        //   // text: '***销量' ,
        //   text: this.barChartOption.chartTitle,
        //   left: 'center',
        //   top: 5,
        //   textStyle: {
        //     color: '#ccc'
        //   }
        // },
        // backgroundColor: '#fff',
        grid: {
          left: '2%',
          right: '2%',
          bottom: '3%',
          containLabel: true
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },

        xAxis: {
          axisLine: {
            show: true,
            lineStyle: {
              type: 'solid',
              color: '#999', // 左边线的颜色
              width: '1'// 坐标线的宽度
            }
          },
          axisLabel: {
            show: true,
            textStyle: {
              color: '#ccc'
            }
          },
          axisTick: {
            show: false
            // //设置地名分割线颜色
            // lineStyle: {
            //   color: '#A9A9A9'
            // }
          },
          // data: ["1月","2月","3月","4月","5月","6月"]
          data: this.barChartOption.xAxis_Data
        },
        yAxis: {
          show: false,
          // 分割线
          splitLine: {
            show: false
            // lineStyle: {
            //   type: 'solid',
            //   color: '#f2f2f2',
            //   width: '1'
            // }
          },
          // 坐标轴标签
          axisLabel: {
            show: false
            // textStyle: {
            //   color: '#ccc'
            // }
          },
          // 轴
          axisLine: {
            show: false
            // lineStyle: {
            //     type: 'solid',
            //     color: '#ccc',//左边线的颜色
            //     width:'1'//坐标线的宽度
            // }
          },
          // 刻度
          axisTick: {
            show: false
            // lineStyle: {
            //   type: 'solid',
            //   color: '#f2f2f2',
            //   width: '1'
            // }
          }
        },
        // color : ['#26a2ff'],
        color: this.barChartOption.itemColor,
        series: [{
          name: '总量',
          type: 'bar',
          barWidth: 15, // 柱图宽度
          // data: [5, 20, 36, 10, 10, 20]
          data: this.barChartOption.series_Data
        }]
      });
      var mOption = myChart.getOption();
      myChart.on('click', function (params) {
        // var data = mOption.series[0].data;
        // var names = mOption.xAxis.data;
        // alert(params.value);
        // mOption.color = 'red';
        // for (var i = 0; i < data.length; i++) {
        //   if (names[i] == params.name) {
        //     data[i] = 40;
        //   }
        // }
        myChart.setOption(mOption);
      });
    },
    // 默认加载
    onloadSize () {
      this.chartW = $(window).width();
    }
  },
  watch: {},
  mounted () {
    // this.$nextTick(function () {
    this.onloadSize();
    this.drawBar();
    // });
  }
};
</script>



