<template>
  <div :id="radarChartOption.chartId" style=" height: 400px" :style="{width:radarChartOption.chartWidth}"></div>
</template>
<style scoped lang="scss">

</style>
<script>
import echarts from 'echarts';

export default {
  props: {
    radarChartOption: ''
  },
  data () {
    return {};
  },
  created () {
  },
  methods: {
    drawRadar (radarChartData) {
      if (radarChartData) {
        this.radarChartOption = radarChartData;
      }
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById(this.radarChartOption.chartId));
      // 绘制图表
      myChart.setOption({

        grid: { // 控制图的大小，调整下面这些值就可以，
          top: 40,
          left: 100,
          right: 150// y2可以控制 X轴跟Zoom控件之间的间隔，避免以为倾斜后造成 label重叠到zoom上
        },
        legend: {
          data: ['机队', '个人']
        },
        radar: {
          // shape: 'circle',
          name: {
            textStyle: {
              color: '#666',
              // backgroundColor: '#999',
              borderRadius: 3,
              padding: [3, 5]
            }
          },
          radius: '50%',
          indicator: this.radarChartOption.indicatorData
        },

        calculable: true,
        color: this.radarChartOption.itemColor,
        series: [
          {
            name: '机队 vs 个人',
            type: 'radar',
            data: this.radarChartOption.series_Data
          }]
      });
    }
  },
  watch: {},
  mounted () {
    // this.$nextTick(function () {
    this.drawRadar();
    // });
  }
};
</script>



