<template>
  <section>
    <v-touch v-on:swiperight="onSwipeRight" class="touchWapper" :swipe-options="{direction: 'horizontal'}">
    <!--头部组件-->
    <com_header :headerTitle="headerTitle"></com_header>
    <div class="c-container">
      <van-cell-group class="my-rank">
        <van-cell title="我的名次" class="my-rank-cell">
          <template>
            <div class="my-rank-cell-value">
            <i class="iconfont icon-shang-arrow mr5 font20"></i>121名
            </div>
          </template>
        </van-cell>
      </van-cell-group>
      <van-cell-group class="rank-list">
        <van-cell v-for="(item,index) in dataList" :key="index" class="rank-list-cell"  >
          <template slot="title">
            <span class="number">{{item.rank}}</span>
            <span class="custom-text">{{item.name}}</span>
          </template>
          <template>
            <span class="value">{{item.score}}</span>
          </template>
        </van-cell>
      </van-cell-group>
    </div>
    </v-touch>
  </section>

</template>
<style scoped lang="scss">

  .my-rank{
    margin-top: 10px;
    .my-rank-cell {
      padding: 15px;
      font-size: 16px;
      .my-rank-cell-value {
        color: #0096ff;
      }
    }
  }
  .rank-list{
    margin-top: 10px;
    .rank-list-cell {
      padding:15px;
      .number{
        font-family: LangDon;
        font-size: 20px;
        color: #ff9700;
        margin-right: 10px;
      }
      .rank-list-cell-value {
        color: #0096ff;
      }
      .value{
        color: #444;
      }
    }
  }
</style>
<script>
// 使用jquery
// import * as indexManageApi from '../../api/main';
import comHeader from '../components/header';

export default {
  // inject: ['reload'],
  data () {
    return {
      headerTitle: '机队排名',
      dataList: []
    };
  },
  components: {
    com_header: comHeader
  },
  created () {
    this.loadData();
  },

  methods: {
    loadData () {
      this.dataList = [
        {id: 1, name: '张伟 ', score: '2011 分', rank: 1},
        {id: 2, name: '王浩 ', score: '1911 分', rank: 2},
        {id: 3, name: '刘云 ', score: '1831 分', rank: 3},
        {id: 4, name: '周彦君 ', score: '1724 分', rank: 4},
        {id: 5, name: '张伟 ', score: '1702 分', rank: 5},
        {id: 6, name: '王华 ', score: '1689 分', rank: 6},
        {id: 7, name: '李梦 ', score: '1618 分', rank: 7}];
    }
  },
  mounted () {
  }
};

</script>



