<template>
  <section>
    <v-touch v-on:swiperight="onSwipeRight" class="touchWapper" :swipe-options="{direction: 'horizontal'}">
    <!--头部组件-->
    <com_header :headerTitle="headerTitle"></com_header>
    <div class="c-container">
      <van-cell-group class="my-info-list">
        <van-cell title="个人资料"  @click="openPage('/Mine/myData')">
          <template >
            <img class="user-img" src="../../assets/images/head_img.jpg" >
          </template>
          <!--<van-icon slot="right-icon" name="friends" class="custom-icon" />-->
        </van-cell>
        <van-cell title="账号"  value="00001" >
        </van-cell>
        <van-cell title="密码"   is-link>
        </van-cell>
        <van-cell title="新消息通知"   is-link>
        </van-cell>
        <van-cell title="清除缓存"  is-link @click="clearCache()">
        </van-cell>
        <van-cell title="关于我们" value="版本0.1.1"  is-link>
        </van-cell>
      </van-cell-group>
        <van-button  class="button-div mt20" plain round  type="primary" @click="loginOut">退出登录</van-button>
    </div>
    </v-touch>
  </section>

</template>
<style scoped lang="scss">
  .my-info-list{
    margin-top: 10px;
      .h-title{
        font-size: 14px;
      }
  }
  .user-img {
    width: 30px;
    height: 30px;
    border-radius: 30px;

    /*border: 2px solid #ffffffe0;*/
  }
  .van-icon{
    font-size: 20px;
  }
  .button-div{
    width: 80%;
    margin-left: 10%;
    height: 30px;
    line-height: 30px;
    color: #28c88c;
    border: 1px solid #28c88c;
  }
</style>
<script>
// 使用jquery
// import * as indexManageApi from '../../api/main';
import comHeader from '../components/header';
import { Dialog } from 'vant';

export default {
  // inject: ['reload'],
  data () {
    return {
      headerTitle: '设置',
      dataList: []
    };
  },
  components: {
    com_header: comHeader
  },
  created () {
    this.loadData();
  },

  methods: {
    loadData () {
      this.dataList = [
        {id: 1, title: '姓名 ', value: '卓易航'}];
    },
    openPage (to) {
      this.$router.push({path: to});
    },
    // 清空缓存
    clearCache () {
      Dialog.confirm({
        title: '',
        message: '确定清除缓存吗？'
      }).then(() => {
        // on confirm
      }).catch(() => {
        // on cancel
      });
    },
    loginOut () {
      this.$store.commit('$_removeStorage');
      this.$router.push('/login');
    }
  },
  mounted () {
  }
};

</script>



