      color: #b5b5b5;
    }
    .f-num{
      font-family: LangDon;
      font-size: 20px;
      color: #28c88c;
    }
  }

</style>

<script>
export default {
  props: {
    baseData: {
      default: []
    },
    desData: {
      default: []
    },
    guideType: {
      default: 'hangqian'
    }
  },
  ready () {
  },
  data () {
    return {
    };
  },
  methods: {
  }
};
</script>



