import axios from 'axios';
// import util from '../utils/util';
import apiUtils from '../utils/api';

let baseOld = '';

// let base = 'http://172.17.2.8:8081/api/v1';

// let base='http://172.17.2.8:8033/e005e0961d7f25179b3e/api/v1';
export const requestLogin = params => { return axios.post(`${baseOld}/login`, params).then(res => res.data); };

// export const requestLoginnew = params => { return axios.get(`http://172.17.2.8:8033/e005e0961d7f25179b3e/api/v1/auth/login`, { params: params }); };
export const requestLoginnew = params => {
  // return axios.post(`${base}/auth/login`, params).then(res => res.data);
  return apiUtils.axiosPost(`/api/v1/app/login`, params);
  // return apiUtils.axiosPost(`${base}/auth/login`, params);
};



