import Vue from 'vue';
import globalLoading from '../components/loading.vue';
export * from './public';
Vue.component('globalLoading-component', globalLoading);

export default {
  created () {
  },
  methods: {
    jumpTo (to) {
      this.$router.push(to);
    },
    // 回退
    onSwipeRight () {
      // this.$store.commit('setTransition', 'turn-off');
      this.$router.go(-1);
    }
  },
  mounted () {
    // var _this = this;
    // window.addEventListener(
    //   'popstate',
    //   function (e) {
    //     _this.$store.commit('setTransition', 'turn-off');
    //   },
    //   false
    // );
  }
};



