<template>
  <section>
    <!--头部组件-->
    <com_header :headerTitle="headerTitle"></com_header>
    <div class="c-container">
      <van-radio-group v-model="radio">
      <van-row >
        <van-col span="24" class="h50 van-col-01 border-b1">
          <span class="span-01">产品建议</span>
          <span class="span-02">
          <van-radio checked-color="#07c160" name="1" />
          </span>
        </van-col>
        <van-col span="24" class="h50 van-col-01 border-b1">
          <span class="span-01">故障反馈</span>
          <span class="span-02">
            <van-radio checked-color="#07c160" name="2" />
          </span>
        </van-col>
        <van-col span="24" class="h50 van-col-01">
          <span class="span-01">其他问题</span>
          <span class="span-02">
            <van-radio checked-color="#07c160" name="3" />
          </span>
        </van-col>
        <van-col span="24" class="h50 van-col-02">
        <span class="span-01">问题描述</span>
        <span class="span-03"></span>
      </van-col>
        <van-col span="24" >
          <van-field
            v-model="message"
            type="textarea"
            placeholder="请输入您的意见或建议！"
            rows="4"
          />
        </van-col>
        <van-col span="24" class="h50 van-col-02">
        <span class="span-01">上传图片</span>
        <span class="span-03">{{dataList.length}}/4</span>
      </van-col>
        <van-col span="24" class="h120" >
          <ul >
            <li class="img-li01" v-for="(item,index) in dataList" :key="index">
              <van-icon class="portrait-icon" :name="item" />
            </li>
          </ul>
          <van-uploader v-show="uploadImg" :after-read="onRead" accept="image/gif, image/jpeg" multiple>
            <van-icon class="portrait-icon" :name="filepath" />
          </van-uploader>
        </van-col>
      </van-row>
      </van-radio-group>
      <div class="button-div mt20">
        <van-button plain round size="large" type="primary">提交</van-button>
      </div>
    </div>
  </section>

</template>
<style scoped lang="scss">
  .my-info-list{
    margin-top: 10px;
      .h-title{
        font-size: 14px;
      }
  }
  .border-b1 {
    border-bottom: 1px solid #99999945;

  }
  .van-col-01{
    height: 50px;
    line-height: 50px;
  }
  .van-col-02{
    line-height: 50px;
    background-color: #f2f2f2;
  }

  .span-01{
    float: left;
    margin-left: 5%;
    width: 20%;
    font-size: 14px;
    text-align: left;
  }
  .span-02{
    float: right;
    margin-right: 5%;
    width: 50%;
    font-size: 14px;
    text-align: right;
    line-height: 35px;
  }
  .portrait-icon{
    width: 40px;
    height: 40px;
  }
  .span-03{
    float: right;
    margin-right: 5%;
    width: 50%;
    font-size: 0.37333rem;
    text-align: right;
    line-height: 40px;
    color: #969799;
  }
  .button-div{
    width: 80%;
    margin-left: 10%;
  }
  .van-uploader{
    font-size: 70px;
    width: 70px;
    height: 100px;
    margin-left: 2%;
    float: left;
    margin-top: 2%;
  }
  .img-li01{
    font-size: 70px;
    width: 80px;
    height: 100px;
    margin-left: 3%;
    margin-top: 2%;
    float: left;
  }
  .img-li01 .portrait-icon{
    width: 70px;
    height: 100px;
  }
</style>
<script>
// 使用jquery
// import * as indexManageApi from '../../api/main';
import comHeader from '../components/header';

export default {
  // inject: ['reload'],
  data () {
    return {
      headerTitle: '意见反馈',
      radio: '1',
      message: '',
      filepath: 'plus',
      dataList: [],
      uploadImg: true
    };
  },
  components: {
    com_header: comHeader
  },
  created () {
  },

  methods: {
    onRead (file) {
      console.log(file);
      // this.filepath = file.content;
      this.dataList.push(file.content);
      if (this.dataList.length === 4) {
        this.uploadImg = false;
      }
    }
  },
  mounted () {
  }
};

</script>



