import axios from 'axios';

import * as util from './util.js';

import { Toast } from 'vant';
// import * as _ from '../util/tool'

// axios 配置
axios.defaults.timeout = 50000;
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';
// axios.defaults.headers.post['Cache-Control'] = 'no-cache';
// axios.defaults.headers.post['Pragma'] = 'no-cache';
axios.defaults.baseURL = window.PLATFORM_CONFIG.baseUrl;

// ['Cache-Control'] = 'no-cache';
// ['Pragma'] = 'no-cache';

// 请求拦截句柄
// let myInterceptor;

// POST传参序列化
axios.interceptors.request.use((config) => {
  // if(config.method  === 'post'){
  //     config.data = qs.stringify(config.data);
  // }
  let token = sessionStorage.getItem('sysToken');
  if (token) { // 判断是否存在token，如果存在的话，则每个http header都加上token
    // config.headers.Authorization = token;
    config.headers.token = token;
    console.log('interceptors config=', config);
  }
  return config;
}, (error) => {
  Toast('错误的传参');
  return Promise.reject(error);
});

// 返回状态判断  // util.catchError 异常处理 在utils/utils.js中
// 原来的异常处理是在每个页面上catch里引用 this.catchReloginMessage(error, error.message); 此方法之前放到mixins/index.js中的
axios.interceptors.response.use((res) => {
  if (!res.data.success) {
    // debugger;
    if (res.data.msg != null && res.data.msg !== '') {
      util.catchMsg(res); // 异常处理中 success=false的信息打印出来
    }
    return Promise.reject(res);
  }
  return res;
}, util.catchError);
