    //   var timeVal = '[' + this.timeValue.join(',') + ']';
    //   this.$emit('input', timeVal); // 这儿必须用input 发送数据，发送的数据会被父级v-model=“test”接受到，再被value=test传回来。
    // }
  },
  watch: {
    'value': function (newVal) {
      this.value = newVal;
    }
  },
  mounted () {
    this.$nextTick(function () {
    });
  }
};
</script>



