// 错误处理
export const catchMsg = function (res) {
  Dialog.alert({
    title: '提示',
    message: res.data.msg
  });
  return Promise.reject(res);
};

// 错误处理
export const catchError = function (error) {
  if (error.response) {
    console.log('===============错误代码' + error.response.status + '===================');
    console.log('=============错误信息=============' + error.response.data.msg);
    switch (error.response.status) {
      case 400:
        Dialog.alert({
          title: '提示',
          message: error.response.data.msg || '请求参数异常'
        });
        break;
      case 401:
        sessionStorage.removeItem('user');
        sessionStorage.removeItem('sysToken');
        this.$router.push('/login');
        Dialog.alert({
          title: '提示',
          message: error.response.data.msg || '密码错误或账号不存在'
        });
        break;
      case 403:
        Dialog.alert({
          title: '提示',
          message: error.response.data.msg || '无访问权限，请联系企业管理员'
        });
        break;
      case 404:
        Dialog.alert({
          title: '提示',
          message: error.response.data.msg || '您所访问的页面或服务不存在'
        });
        break;
      default:
        Dialog.alert({
          title: '提示',
          message: error.response.data.msg || '服务端异常，请联系技术支持'
        });
        // Dialog.alert({
        //   title: '标题',
        //   message: '弹窗内容'
        // }).then(() => {
        //   // on close
        // });
    }
  } else if (!error.response && error.message === 'Network Error') {
    Dialog.alert({
      title: '提示',
      message: '网络连接超时!'
    });
  }
  return Promise.reject(error);
};

var SIGN_REGEXP = /([yMdhsm])(\1*)/g;
var DEFAULT_PATTERN = 'yyyy-MM-dd';

function padding (s, len) {
  len = len - (s + '').length;
  for (var i = 0; i < len; i++) {
    s = '0' + s;
  }
  return s;
};

export default {
  getQueryStringByName: function (name) {
    var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
    var r = window.location.search.substr(1).match(reg);
    var context = '';
    if (r != null) {
      context = r[2];
    }
    reg = null;
    r = null;
    return context == null || context === '' || context === 'undefined' ? '' : context;
  },
  formatDate: {

    format: function (date, pattern) {
      pattern = pattern || DEFAULT_PATTERN;
      return pattern.replace(SIGN_REGEXP, function ($0) {
        switch ($0.charAt(0)) {
          case 'y':
            return padding(date.getFullYear(), $0.length);
          case 'M':
            return padding(date.getMonth() + 1, $0.length);
          case 'd':
            return padding(date.getDate(), $0.length);
          case 'w':
            return date.getDay() + 1;
          case 'h':
            return padding(date.getHours(), $0.length);
          case 'm':
            return padding(date.getMinutes(), $0.length);
          case 's':
            return padding(date.getSeconds(), $0.length);
        }
      });
    },
    parse: function (dateString, pattern) {
      var matchs1 = pattern.match(SIGN_REGEXP);
      var matchs2 = dateString.match(/(\d)+/g);
      if (matchs1.length === matchs2.length) {
        var _date = new Date(1970, 0, 1);
        for (var i = 0; i < matchs1.length; i++) {
          var _int = parseInt(matchs2[i]);
          var sign = matchs1[i];
          switch (sign.charAt(0)) {
            case 'y':
              _date.setFullYear(_int);
              break;
            case 'M':
              _date.setMonth(_int - 1);
              break;
            case 'd':
              _date.setDate(_int);
              break;
            case 'h':
              _date.setHours(_int);
              break;
            case 'm':
              _date.setMinutes(_int);
              break;
            case 's':
              _date.setSeconds(_int);
              break;
          }
        }
        return _date;
      }
      return null;
    }

  }

};



