            <p class="m-info-num"  @click="jumpToHistory(1)">{{myDataJson.items.total}}<span>元</span></p>
            <van-row gutter="20">
              <van-col span="8">
                <div class="jiangli-tag">
                  <p>操作奖励</p>
                  <p class="num">{{myDataJson.items.itemList.operReward}}<span>元</span></p>
                  <p @click="toRanking()">排名<span class="color-tag green-tag">{{myDataJson.items.itemList.operRanking}}<i class="iconfont icon-arrow-top"></i></span></p>
                </div>
              </van-col>
              <van-col span="8">
                <div class="jiangli-tag">
                  <p>航班奖励</p>
                  <p class="num">{{myDataJson.items.itemList.flightRanking}}<span>元</span></p>
                  <p @click="toRanking()">排名 <span class="color-tag red-tag">{{myDataJson.items.itemList.flightRanking}}<i class="iconfont icon-arrow-bottom"></i></span></p>
                </div>
              </van-col>
              <van-col span="8">
                <div class="jiangli-tag">
                  <p>运行奖励</p>
                  <p class="num">{{myDataJson.items.itemList.runReward}}<span>元</span></p>
                  <p @click="toRanking()">排名<span class="color-tag blue-tag">{{myDataJson.items.itemList.runRanking}}</span></p>
                </div>
              </van-col>
            </van-row></div>
      <van-row>
        <van-col span="12">
          <div class="my-data-block mdb-blue"  @click="jumpToHistory(2)">
          <p class="mdb-title1">
            <span class="mdb-title-icon1"> <i class="iconfont icon-youliang2 "></i></span>节油操作项目</p>
