              <li>实施 <div class="num">135/150</div></li>
              <li>平均质量 <div class="num">81</div></li>
              <li>节油总量 <div class="num">3757<span>kg</span></div></li>
              <li>个人分配 <div class="num">1025<span>kg</span></div></li>
              <li>奖励 <div class="num">1522<span>元</span></div></li>
            </ul>
        </div>
        </van-col>
        <van-col span="12">
          <div class="my-data-block"  @click="jumpToHistory(3)">
          <p class="mdb-title1">
            <span class="mdb-title-icon2"> <i class="iconfont icon-fj-2 "></i></span>本月航班</p>
          <van-row gutter="20">
            <van-col span="24">
              <div class="jiangli-tag">
                <p class="num">35<span>班次</span></p>
              </div>
            </van-col>
          </van-row>
        </div>
          <div class="my-data-block"  @click="jumpToHistory(3)">
            <p class="mdb-title">航班节油</p>
            <div class="jiangli-tag">
