            <p class="mdb-title">奖励</p>
            <div class="jiangli-tag">
              <p class="num">998<span>元</span></p>
            </div>
          </div>
        </van-col>
      </van-row>
      <van-row>
        <van-col span="24">
          <div class="my-data-block"  @click="jumpToHistory(4)">
            <p class="mdb-title1">
              <span class="mdb-title-icon3"> <i class="iconfont icon-kaohe "></i></span>运行质量考核</p>
            <van-row gutter="20">
              <van-col span="12">
                <div class="jiangli-tag text-center">
                  <p class="num text-center">1257<span>元</span></p>
                  <p>运行质量奖励</p>
                </div>
              </van-col>
              <van-col span="12">
                <div class="jiangli-tag text-center">
                  <p class="num text-center">65<span>h</span></p>
                  <p>飞行小时</p>
                </div>
              </van-col>
            </van-row>
          </div>
        </van-col>
      </van-row>
    </div>
    </v-touch>
  </section>

