.my-data{
  background: #4f5064;
  padding-bottom: 40px;
}
  .my-data-title{
    font-size: 24px;
    color: #fff;
    padding-left: 10px;
    font-family: "SimHei";
  }
  .my-data-block{
    margin: 10px;
    background: #fff;
    border-radius: 5px;
    color: #444;
    padding: 10px;
    font-size: 14px;
  }
  .mdb-blue{
    background: #7e93d4;
    color: #fff;
    font-size: 14px;
    .blue-ul{
      li{
        position: relative;
        line-height: 46px;
        .num{
          position: absolute;
          width: 60px;
          top: 0;
          right: 0px;
          text-align: left;
          font-family: LangDon;
          font-size: 20px;
          span {
            font-size: 14px;
            margin-left: 5px;
            font-family: 'Microsoft Yahei';
          }
        }
      }
    }
  }
.mdb-title{
  position: relative;
  margin-bottom:5px;
  font-size: 14px;
  .mdb-title-r{
    position: absolute;
    right: 0px;
    color: #999;
  }
}
  .m-info-num {
    margin-top: 15px;
    margin-bottom:15px;
    text-align: left;
    font-family: LangDon;
    font-size: 40px;
    color: #4f5064;
    span {
      font-size: 14px;
      margin-left: 10px;
    }
  }

.jiangli-tag {
  color: #b5b5b5;
  font-size: 14px;
  text-align: left;
  p {
    margin-bottom: 10px;
    line-height: 20px;
    font-size: 14px;
  }
  .num {
    text-align: left;
    font-family: LangDon;
    font-size: 22px;
    color: #505165;
    span {
      font-size: 14px;
      margin-left: 10px;
      font-family: 'Microsoft Yahei';
    }
  }
  .color-tag {
    color: #fff;
    border-radius: 10px;
    margin-left: 5px;
    width: 40px;
    text-align: center;
    display: inline-block;
    font-size: 12px;
    i {
      font-size: 12px;
    }
  }
  .color-tag.green-tag {
    background: #26c88c;
  }

  .color-tag.red-tag {
    background: #ff9600;
  }

  .color-tag.blue-tag {
    background: #7d92d3;
  }
}
  .mdb-title1{
    font-size: 16px;
    margin-bottom: 20px;
  }
  .mdb-title-icon1{
      background: #fff;
      color: #7e93d4;
      border-radius: 50%;
      width: 24px;
    font-size: 14px;
      height:24px;
      line-height: 24px;
      text-align: center;
      display: inline-block;
      margin-right: 5px;
    }
.mdb-title-icon2{
  background: #28c88c;
  color: #fff;
  border-radius: 50%;
  width: 24px;
  height:24px;
  line-height: 24px;
  font-size: 14px;
  text-align: center;
  display: inline-block;
  margin-right: 5px;
}
.mdb-title-icon3{
  background: #ffca00;
  color: #fff;
  border-radius: 50%;
  width: 24px;
  font-size: 14px;
  height:24px;
  line-height: 24px;
  text-align: center;
  display: inline-block;
  margin-right: 5px;
}
</style>
<script>
// 使用jquery
// import * as indexManageApi from '../../api/main';
import comHeader from '../components/header';
import myDataJson from './myData.json';

export default {
  // inject: ['reload'],
  data () {
    return {
      headerTitle: '',
      dataList: [],
      hearderColor: 'black',
      myDataJson: myDataJson
    };
  },
  components: {
    com_header: comHeader
  },
  created () {
    this.loadData();
  },

  methods: {
    loadData () {
      this.dataList = [
        {id: 1, title: '姓名 ', value: '卓易航'}];
    },
    jumpToHistory (iType) {
      this.$router.push({
        path: '/Main/RewardHistory',
        query: {
          iType: iType
        }
      });
    },
    toRanking () {
      this.$router.push({
        path: '/Mine/myRanking',
        query: {
        }
      });
    }
  },
  mounted () {
  }
};

</script>



