import apiUtils from '../utils/api';

export const getAanalyze = {
  p: ['get,/api/v1/app/flights/kpi/analyze'],
  r: params => {
    return apiUtils.axiosGet(`/api/v1/app/flights/kpi/analyze` + params, '');
  }
};
export const weatherAnalyze = {
  p: ['get,/api/v1/app/weatherAnalyze'],
  r: params => {
    return apiUtils.axiosGet(`/api/v1/app/weatherAnalyze` + params, '');
  }
};

export const flightdataJson = {
  p: ['get,/api/v1/app/flightdataJson'],
  r: params => {
    return apiUtils.axiosGet(`/api/v1/app/flightdataJson` + params, '');
  }
};
export const getparamlist = {
  p: ['get,/api/v1/app/getparamlist'],
  r: params => {
    return apiUtils.axiosGet(`/api/v1/app/getparamlist` + params, '');
  }
};
