                <span class="flightinfo-span02">节油量</span></van-col>
              <van-col span="22" style="border-bottom: 1px solid #ababab;margin-left: 3%;">
                <span class="flightinfo-span03">预计</span>
                <span class="flightinfo-span03">低于 7 min</span>
                <span class="flightinfo-span03">3.62kg</span>
              </van-col>
