          </li>
          <li class="flightinfo_li">
          <van-row>
            <van-col span="24" style="border-bottom: 1px solid #ababab;"><span class="flightinfo-span01"><strong>BP 1</strong> 划出时长</span></van-col>
            <van-col span="22" style="margin-left: 3%;">
              <span class="flightinfo-span02"></span>
              <span class="flightinfo-span02">操作执行</span>
              <span class="flightinfo-span02">节油量</span></van-col>
            <van-col span="22" style="border-bottom: 1px solid #ababab;margin-left: 3%;">
