<script>
import BMap from 'BMap';
// import $ from 'jquery';
import BMAP_NORMAL_MAP from 'BMAP_NORMAL_MAP';
import BMAP_HYBRID_MAP from 'BMAP_HYBRID_MAP';
import BMAP_ANCHOR_TOP_RIGHT from 'BMAP_ANCHOR_TOP_RIGHT';
import comHeader from '../components/header';

export default {
  name: 'App',
  data () {
    return {
      buttoncontrolimg01: false,
      buttoncontrolimg02: true,
      buttoncontrolimg03: false,
      headerTitle: 'ZBAA-ZSSS'
    };
  },
  created () {
  },
  components: {
    com_header: comHeader
  },
  methods: {
    map: function () {
      let map = new BMap.Map(this.$refs.allmap);// 创建Map实例//map.centerAndZoom(new BMap.Point(116.404, 39.915), 11);// 初始化地图,设置中心点坐标和地图级别
      // map.addControl(new BMap.MapTypeControl({// 添加地图类型控件
      //   mapTypes: [
      //     BMAP_NORMAL_MAP, BMAP_HYBRID_MAP
      //   ]
      // }));
      // 绘制航线
      this.drawRoutes(map);
      //  创建航标
      this.createSign(map);
      // 备降机场
      this.alternateAirport(map);
      // 天气信息
      this.weatherInfo(map);
      // 地图
      this.mapControl(map);
    },
    mapControl (map) {
      // 定义一个控件类,即function
      function ZoomControl () {
        // 默认停靠位置和偏移量
        this.defaultAnchor = BMAP_ANCHOR_TOP_RIGHT;
        this.defaultOffset = new BMap.Size(10, 20);
      }

      // 通过JavaScript的prototype属性继承于BMap.Control
      ZoomControl.prototype = new BMap.Control();

      // 自定义控件必须实现自己的initialize方法,并且将控件的DOM元素返回
      // 在本方法中创建个div元素作为控件的容器,并将其添加到地图容器中
      ZoomControl.prototype.initialize = function (map) {
        // 创建一个DOM元素
        // var div = document.createElement('div');
        var div = document.getElementById('buttoncontrol01');
        // 添加文字说明
        // div.appendChild(document.createTextNode('气象'));
        // 设置样式
        div.style.cursor = 'pointer';
        div.classList.add('backgroundImg01');
        // 绑定事件,点击一次放大两级
        div.onclick = function (e) {
          // map.setZoom(map.getZoom() + 2);
          map.setMapType(BMAP_HYBRID_MAP);// 设置地图类型
        };
        // 添加DOM元素到地图中
        map.getContainer().appendChild(div);

        // 将DOM元素返回
        return div;
      };
      // 创建控件
      var myZoomCtrl = new ZoomControl();
      // 添加到地图当中
      map.addControl(myZoomCtrl);

      // 定义一个控件类,即function
      function ZoomControl02 () {
        // 默认停靠位置和偏移量
        this.defaultAnchor = BMAP_ANCHOR_TOP_RIGHT;
        this.defaultOffset = new BMap.Size(10, 46);
      }

      // 通过JavaScript的prototype属性继承于BMap.Control
      ZoomControl02.prototype = new BMap.Control();

      // 自定义控件必须实现自己的initialize方法,并且将控件的DOM元素返回
      // 在本方法中创建个div元素作为控件的容器,并将其添加到地图容器中
      var _this = this;
      ZoomControl02.prototype.initialize = function (map) {
        // 创建一个DOM元素
        // var div = document.createElement('div');
        var div = document.getElementById('buttoncontrol02');
        // 添加文字说明
        // div.appendChild(document.createTextNode('地图'));
        // 设置样式
        div.style.cursor = 'pointer';
        div.onclick = function (e) {
          if (_this.buttoncontrolimg02 === true) {
            _this.buttoncontrolimg02 = false;
            map.setMapType(BMAP_HYBRID_MAP);// 设置地图类型
          } else {
            _this.buttoncontrolimg02 = true;
            map.setMapType(BMAP_NORMAL_MAP);// 设置地图类型
          }
        };
        // 添加DOM元素到地图中
        map.getContainer().appendChild(div);
        // 将DOM元素返回
        return div;
      };
      // 创建控件
      var ZoomControls = new ZoomControl02();
      // 添加到地图当中
      map.addControl(ZoomControls);

      // 定义一个控件类,即function
      function ZoomControl03 () {
        // 默认停靠位置和偏移量
        this.defaultAnchor = BMAP_ANCHOR_TOP_RIGHT;
        this.defaultOffset = new BMap.Size(10, 72);
      }

      // 通过JavaScript的prototype属性继承于BMap.Control
      ZoomControl03.prototype = new BMap.Control();

      // 自定义控件必须实现自己的initialize方法,并且将控件的DOM元素返回
      // 在本方法中创建个div元素作为控件的容器,并将其添加到地图容器中
      ZoomControl03.prototype.initialize = function (map) {
        // 创建一个DOM元素
        // var div = document.createElement('div');
        var div = document.getElementById('buttoncontrol03');
        // 添加文字说明
        // div.appendChild(document.createTextNode('地图'));
        // 设置样式
        div.style.cursor = 'pointer';
        var pointA = new BMap.Point(123.349442, 41.794481);// 创建点坐标
        var pointC = new BMap.Point(117.140354, 36.572421);// 创建点坐
        var polylines = new BMap.Polyline([pointA, pointC], {
          strokeColor: '#0096ff',
          strokeWeight: 1,
          strokeOpacity: 1,
          setStrokeStyle: 'dashed'
        }); // 定义折线
          // 绑定事件
        div.onclick = function (e) {
          if (_this.buttoncontrolimg03 === true) {
            _this.buttoncontrolimg03 = false;
            polylines.enableMassClear();// 可清除
            map.addOverlay(polylines); // 添加折线到地图上
          } else {
            _this.buttoncontrolimg03 = true;
            map.clearOverlays();
            // 绘制航线
            _this.drawRoutes(map);
            _this.alternateAirport(map);
          }
        };
        // 添加DOM元素到地图中
        map.getContainer().appendChild(div);

        // 将DOM元素返回
        return div;
      };
      // 创建控件
      var ZoomControlt = new ZoomControl03();
      // 添加到地图当中
      map.addControl(ZoomControlt);
    },
    drawRoutes (map) {
      var pointA = new BMap.Point(123.349442, 41.794481);// 创建点坐标
      var pointB = new BMap.Point(116.395524, 39.931414);// 创建点坐
      var pointC = new BMap.Point(117.140354, 36.572421);// 创建点坐
      var pointD = new BMap.Point(118.777049, 32.058853);// 创建点坐
      var point = new BMap.Point(117.140354, 36.572421); // 创建点坐标
      map.centerAndZoom(point, 6);
      // map.centerAndZoom('北京', 6);// 设置地图显示的城市
      // map.enableScrollWheelZoom(true);//开启鼠标滚轮缩放
      map.enableScrollWheelZoom();

      map.enableDragging();// 两秒后开启拖拽
      var polyline = new BMap.Polyline([pointA, pointB, pointC, pointD], {
        strokeColor: '#0096ff',
        strokeWeight: 1,
        strokeOpacity: 1
      }); // 定义折线
      map.addOverlay(polyline); // 添加折线到地图上
    },
    // 创建标记
    createSign (map) {
      var pointA = new BMap.Point(123.349442, 41.794481);// 创建点坐标
      var pointB = new BMap.Point(116.395524, 39.931414);// 创建点坐
      var pointC = new BMap.Point(117.140354, 36.572421);// 创建点坐
      var pointD = new BMap.Point(118.777049, 32.058853);// 创建点坐
      var label = new BMap.Label('B12', {position: pointB, offset: new BMap.Size(-5, 15)});
      label.setStyle({
        color: '#fff',
        backgroundColor: '#0096ff',
        border: '0',
        fontSize: '12px',
        // width: '200px',
        opacity: '0.8',
        verticalAlign: 'center',
        borderRadius: '6px',
        whiteSpace: 'normal',
        wordWrap: 'break-word',
        padding: '3px'
      });
      label.disableMassClear();// 不可删除
      map.addOverlay(label);
      // 第二个
      var label02 = new BMap.Label('BP2', {position: pointC, offset: new BMap.Size(-5, 15)});
      label02.setStyle({
        color: '#0096ff',
        backgroundColor: '#fafafa',
        border: '0',
        fontSize: '12px',
        // width: '200px',
        opacity: '0.8',
        verticalAlign: 'center',
        borderRadius: '6px',
        whiteSpace: 'normal',
        wordWrap: 'break-word',
        padding: '3px'
      });
      label02.disableMassClear();// 不可删除
      map.addOverlay(label02);

      // 起飞机场
      var label03 = new BMap.Label('QFJC', {position: pointA, offset: new BMap.Size(7, -10)});
      label03.setStyle({
        color: 'black',
        backgroundColor: '#fafafa00',
        border: '0',
        fontSize: '12px',
        // width: '200px',
        opacity: '1'
        // verticalAlign: 'center',
        // borderRadius: '6px',
        // whiteSpace: 'normal',
        // wordWrap: 'break-word',
        // padding: '3px'
      });
      label03.disableMassClear();// 不可删除
      map.addOverlay(label03);
      // 目的机场
      var label04 = new BMap.Label('MDJC', {position: pointD, offset: new BMap.Size(7, -10)});
      label04.setStyle({
        color: 'black',
        backgroundColor: '#fafafa00',
        border: '0',
        fontSize: '12px',
        // width: '200px',
        opacity: '1'
        // verticalAlign: 'center',
        // borderRadius: '6px',
        // whiteSpace: 'normal',
        // wordWrap: 'break-word',
        // padding: '3px'
      });
      label04.disableMassClear();// 不可删除
      map.addOverlay(label04);
    },
    // 备降机场
    alternateAirport (map) {
      var point = new BMap.Point(121.416382, 31.261); // 创建点坐标
      var circle = new BMap.Circle(point, 50000, {fillColor: '', strokeWeight: 1, fillOpacity: 1, strokeOpacity: 1});
      var circle02 = new BMap.Circle(point, 100000, {
        fillColor: '',
        strokeWeight: 1,
        fillOpacity: 1,
        strokeOpacity: 1
      });
      var circle03 = new BMap.Circle(point, 150000, {
        fillColor: '',
        strokeWeight: 1,
        fillOpacity: 1,
        strokeOpacity: 1
      });
      var circle04 = new BMap.Circle(point, 200000, {
        fillColor: '',
        strokeWeight: 1,
        fillOpacity: 1,
        strokeOpacity: 1
      });
      map.addOverlay(circle);// 增加圆
      map.addOverlay(circle02);// 增加圆
      map.addOverlay(circle03);// 增加圆
      map.addOverlay(circle04);// 增加圆
    },
    // 创建天气窗口对象
    weatherInfo: function (map) {
      var sContent = document.getElementById('weatherInfo01').innerHTML;
      var sContenta = document.getElementById('weatherInfo02').innerHTML;
      var point = new BMap.Point(116.404, 39.915);
      // var opts = {
      //   width: 20, // 信息窗口宽度
      //   height: 10 // 信息窗口高度
      // };
      // var infoWindow = new BMap.InfoWindow(sContent, opts); // 创建信息窗口对象
      // map.openInfoWindow(infoWindow, point); // 开启信息窗口

      var label02 = new BMap.Label(sContent, {position: point, offset: new BMap.Size(-20, -25)});
      label02.setStyle({
        color: 'black',
        backgroundColor: '#fafafa',
        border: '0',
        fontSize: '14px',
        // width: '200px',
        opacity: '1',
        verticalAlign: 'center',
        borderRadius: '6px',
        whiteSpace: 'normal',
        wordWrap: 'break-word',
        padding: '3px',
        width: '45px',
        height: '20px'
      });
      label02.disableMassClear();// 不可删除
      map.addOverlay(label02);
      // 霾
      var pointa = new BMap.Point(121.416382, 31.261); // 创建点坐标
      var label03 = new BMap.Label(sContenta, {position: pointa, offset: new BMap.Size(-20, -25)});
      label03.setStyle({
        color: 'wheat',
        backgroundColor: '#fa8722',
        border: '0',
        fontSize: '14px',
        // width: '200px',
        opacity: '1',
        verticalAlign: 'center',
        borderRadius: '6px',
        whiteSpace: 'normal',
        wordWrap: 'break-word',
        padding: '3px',
        width: '45px',
        height: '20px'
      });
      label03.disableMassClear();// 不可删除
      map.addOverlay(label03);
    }
  },
  mounted () {
    this.map();
  },
  activated () {
    this.map();
  },
  watch: {}
};
</script>



