<template>
  <div>
      <div class="e-card-box">
        <ul class="e-card-row">
          <div class="ling-bg"></div>
          <li :class="ecardActive==0?'is-active':''" @click="openRadar(0)">
            <div class="icon-with-line">
              <i class="iconfont icon-weibiaoti--20  line-icon"></i>
            </div>
            <!--<span>ZSSS</span>-->
          </li>
          <li :class="ecardActive==1?'is-active':''" @click="openRadar(1)">
            <div class="icon-with-line">
              <!--<i class="iconfont icon-weibiaoti&#45;&#45;19  line-icon"></i>-->
            </div>
            <!--<span>起飞</span>-->
          </li>
          <li :class="ecardActive==2?'is-active':''" @click="openRadar(2)">
            <div class="icon-with-line">
              <i class="iconfont icon-weibiaoti--21  line-icon"></i>
            </div>
            <!--<span>巡航</span>-->
          </li>
          <li :class="ecardActive==3?'is-active':''" @click="openRadar(3)">
            <div class="icon-with-line">
              <!--<i class="iconfont icon-weibiaoti&#45;&#45;18  line-icon"></i>-->
            </div>
            <!--<span>进近</span>-->
          </li>
          <li :class="ecardActive==4?'is-active':''" @click="openRadar(4)">
            <div class="icon-with-line">
              <div class="xie-line  xie-line5"></div>
              <i class="iconfont icon-weibiaoti--22  line-icon"></i>
            </div>
            <!--<span>ZGSZ</span>-->
          </li>
        </ul>
      </div>

      <van-tabs class="sail-tab"  @click="openRadar" @scroll="openRadar"  @change="openRadar"  v-model="ecardActive" swipeable>
        <van-tab title="滑出">
          <sfGuide :guideStatus="0" :guideData="guideData" :guideType="guideType"></sfGuide>
        </van-tab>
        <van-tab title="起飞" >
          <sfGuide :guideStatus="1" :guideData="guideData" :guideType="guideType"></sfGuide>
        </van-tab>
        <van-tab title="巡航" >
          <sfGuide :guideStatus="2" :guideData="guideData" :guideType="guideType"></sfGuide>
        </van-tab>
        <van-tab title="下降">
          <sfGuide :guideStatus="3" :guideData="guideData" :guideType="guideType"></sfGuide>
        </van-tab>
        <van-tab title="滑入">
          <sfGuide :guideStatus="4" :guideData="guideData" :guideType="guideType"></sfGuide>
        </van-tab>
      </van-tabs>
  </div>
</template>

<style lang="scss" scoped>
  .s-title{
    line-height: 30px;
    margin-bottom: 20px;
    font-size: 16px;
    box-sizing: border-box;
    position: relative;
    i{
      color: #2aca8e;
      margin-right: 5px;
      font-size: 20px;
    }
  }
  .s-title-r{
    position: absolute;
    right: 0;
    line-height: 30px;
    font-size: 14px;
    color: #999;
    i{
      font-size: 14px;
      color: #999;
    }
  }
</style>

<script>
import sfGuide from './sf_guide';
import guideDataAll from './../sailGuideData.json';
export default {
  props: {
    baseData: {
      default: []
    },
    guideType: {
      default: 'hangqian'
    }
  },
  created () {
    this.openRadar(1);
  },
  data () {
    return {
      ecardActive: 1,
      guideDataAll: guideDataAll,
      guideData: []
    };
  },
  components: {
    sfGuide: sfGuide
  },
  methods: {

    openRadar (val) {
      this.ecardActive = val;
      switch (val) {
        case 0:
          this.guideData = this.guideDataAll.huachu;
          break;
        case 1:
          this.guideData = this.guideDataAll.qifei;
          break;
        case 2:
          this.guideData = this.guideDataAll.xunhang;
          break;
        case 3:
          this.guideData = this.guideDataAll.xiajiang;
          break;
        case 4:
          this.guideData = this.guideDataAll.huaru;
          break;
        default:
          this.guideData = this.guideDataAll.huachu;
      }
    }
  }
};
</script>



