<template>
  <section>
    <v-touch v-on:swiperight="onSwipeRight" class="touchWapper" :swipe-options="{direction: 'horizontal'}">
    <!--头部组件-->
    <com_header :headerTitle="headerTitle"></com_header>
    <van-pull-refresh  class="c-container" v-model="isLoading" @refresh="onRefresh">
      <van-cell-group class="zz-list"  v-for="(item,index) in dataList" :key="index">
        <van-cell title="执照名称:" title-class="zz-title" value-class="zz-value"  :value="item.iTEM_NAME" />
        <!--<van-cell title="机型:"   title-class="zz-title" value-class="zz-value" />-->
        <!--<van-cell title="开始日期:"  title-class="zz-title" value-class="zz-value"  :value="item.eFFECT_DATE" />-->
        <van-cell title="授权日期:"  title-class="zz-title" value-class="zz-value"  :value="item.eFFECT_DATE" />
        <van-cell title="过期日期:"  title-class="zz-title" value-class="zz-value"  :value="item.eXPIRE_DATE" />
        <div class="attachment-file">附件<i class="iconfont icon-jiantouright"></i></div>
      </van-cell-group>
    </van-pull-refresh>
    </v-touch>
  </section>
</template>
<style scoped lang="scss">
  .zz-list{
    margin: 10px;
    border-radius: 4px;
    font-size: 14px;
  }
  .attachment-file{
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    text-align: center;
    color: #0096ff;
  }

</style>
<script>
// 使用jquery
// import * as indexManageApi from '../../api/main';
import comHeader from '../components/header';
import * as mineApi from '../../api/mine';

export default {
  // inject: ['reload'],
  data () {
    return {
      isLoading: false,
      headerTitle: '执照信息',
      dataList: [],
      zzTitle: 'zz-title'
    };
  },
  components: {
    com_header: comHeader
  },
  created () {
    this.loadData();
  },
  methods: {
    loadData () {
      var para = '?avrId=A00096';
      mineApi.getlicense.r(para).then(res => {
        if (res) {
          this.dataList = res.data;
          for (let i in this.dataList) {
            var eDate = this.dataList[i].eXPIRE_DATE;
            if (eDate !== undefined && eDate !== '') {
              this.dataList[i].ex_long = this.diffTime(eDate);
            } else {
              this.dataList[i].ex_long = 0;
            }
          }
        } else {
          this.$message({
            message: '获取失败',
            type: 'error'
          });
        }
      }).catch(() => {
        // .catch((error)
        //   this.listLoading = false;
        //                 错误处理统一到utils/api.js 中通过拦截器调取utils/utils.js的catchError方法进行处理
      });
      // this.dataList = [
      //   {id: 1, name: '商用驾驶员执照 ', ftype: 'A319/320', sDate: '2016-09-09', authDate: '2016-05-05', eDate: '2022-05-05'},
      //   {id: 2, name: '商用驾驶员执照 ', ftype: 'A319/320', sDate: '2016-09-09', authDate: '2016-05-05', eDate: '2022-05-05'}];
    },
    onRefresh () {
      setTimeout(() => {
        // this.$toast('刷新成功');
        this.isLoading = false;
        this.loadData();
        // this.count++;
      }, 500);
    }
  },
  mounted () {
  }
};

</script>



