<template>
  <section>
    <v-touch v-on:swiperight="onSwipeRight" class="touchWapper" :swipe-options="{direction: 'horizontal'}">
    <!--头部组件-->
    <!--"cATEGORY":"LICENSECATEGORY",-->
    <!--"eFFECT_DATE":"2018-02-23 00:00:00",-->
    <!--"eXPIRE_DATE":"2019-02-22 00:00:00",-->
    <!--"hR_ID":"A00096",-->
    <!--"iTEM_NAME":"国际健康证",-->
    <!--"qUALIFICATION_NUMBER":"230121198304012016",-->
    <!--"rEMARKS":""-->
    <com_header :headerTitle="headerTitle"></com_header>
    <div class="c-container">
      <van-cell-group class="info-list">
        <van-cell v-for="(item,index) in dataList" :key="index" class="info-list-cell" title-class="zz-title" value-class="zz-value" is-link>
          <template slot="icon">
            <i class="iconfont icon-zz-5 zz-icon"></i>
          </template>
          <template slot="title">
            <span class="title">{{item.iTEM_NAME}}</span>
          </template>
          <template>
            <span class="value font-red" v-if="item.ex_long>= 0 && item.ex_long<10">还有 {{item.ex_long}}天 到期</span>
            <!--<span class="value font-red" v-if="item.ex_long>= 0 && item.ex_long<10">未到期</span>-->
            <span class="value  font-red"  v-if="item.ex_long<0">已过期</span>
            <!--<span class="value"   v-if="item.ex_long >=10">还有 {{item.ex_long}}天 到期</span>-->
            <span class="value"   v-if="item.ex_long >=10">未到期</span>
          </template>
        </van-cell>
      </van-cell-group>
    </div>
    </v-touch>
  </section>
</template>
<style scoped lang="scss">

  .info-list{
    margin-top: 10px;
    font-size: 14px;
    .info-list-cell {
      padding:15px;
      .title{
        /*color: #999;*/
        font-size: 14px;
      }
      .value{
        color: #999;
      }
      .font-red{
        color: #ff1013!important;

      }
    }
  }
  .zz-icon{
    color: #0096ff;
    font-size: 20px;
    margin-right: 10px;
  }
</style>
<script>
// 使用jquery
// import * as indexManageApi from '../../api/main';
import comHeader from '../components/header';
import * as mineApi from '../../api/mine';
export default {
  // inject: ['reload'],
  data () {
    return {
      headerTitle: '我的资质',
      dataList: []
    };
  },
  components: {
    com_header: comHeader
  },
  created () {
    this.loadData();
  },

  methods: {
    loadData () {
    //   this.dataList = [
    //     {id: 1, title: '执照信息 ', value: '7'},
    //     {id: 2, title: '体检合格证 ', value: '22'},
    //     {id: 3, title: 'ICAO英语 ', value: '15'},
    //     {id: 4, title: '大陆居民往来台湾通行证 ', value: '15'},
    //     {id: 5, title: '港澳通行证 ', value: '15'},
    //     {id: 6, title: '港澳通行证签注 ', value: '15'},
    //     {id: 7, title: '国际护照 ', value: '15'}];
    // }
      var para = '?avrId=A00096';
      mineApi.getPilotQualificat.r(para).then(res => {
        if (res) {
          // this.total = res.data.pageData.totalCount;
          this.dataList = res.data;
          for (let i in this.dataList) {
            var eDate = this.dataList[i].eXPIRE_DATE;
            if (eDate !== undefined && eDate !== '') {
              this.dataList[i].ex_long = this.diffTime(eDate);
            } else {
              this.dataList[i].ex_long = 0;
            }
          }
          // this.listLoading = false;
        } else {
          this.$message({
            message: '获取失败',
            type: 'error'
          });
        }
      }).catch(() => {
      // .catch((error)
      //   this.listLoading = false;
        //                 错误处理统一到utils/api.js 中通过拦截器调取utils/utils.js的catchError方法进行处理
      });
    },
    diffTime (endDate) {
      var endDateNew = endDate.substring(0, 19).toString().replace(/-/g, '/');
      var diff = new Date(endDateNew).getTime() - new Date().getTime();// 时间差的毫秒数
      // 计算出相差天数
      var days = Math.floor(diff / (24 * 3600 * 1000));

      // 计算出小时数
      var leave1 = diff % (24 * 3600 * 1000); // 计算天数后剩余的毫秒数
      var hours = Math.floor(leave1 / (3600 * 1000));
      var returnStr = '';
      if (days > 0) {
        if (hours > 0) {
          days = days + 1;
        }
        returnStr = days;
      }
      return returnStr;
    }
  },
  mounted () {
  }
};

</script>



