import MyData from '../views/main/MyData.vue';
import SailFront from '../views/main/sailFront.vue';
import SailAfter from '../views/main/sailAfter.vue';
import RewardHistory from '../views/main/rewardHistory.vue';
// 首页
export default [
  {path: '/Main/SailFront', component: SailFront, name: '/SailFront', alias: '航前'},
  {path: '/Main/SailAfter', component: SailAfter, name: '/SailAfter', alias: '航后'},
  {path: '/Main/RewardHistory', component: RewardHistory, name: '/RewardHistory', alias: '奖励历史'},
  {path: '/Main/MyData', component: MyData, name: '/MyData', alias: '我的数据'}
];



