<template>
  <div class="loadingbox" v-if="showLoading" ><van-loading color="white" class="loading-circle" ></van-loading></div>
</template>
<style scoped lang="scss">
  .loadingbox{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /*background-color:#000000;!* IE6和部分IE7内核的浏览器(如QQ浏览器)下颜色被覆盖 *!*/
    /*background-color:rgba(0,0,0,0.4); !* IE6和部分IE7内核的浏览器(如QQ浏览器)会读懂，但解析为透明 *!*/
    z-index: 9999;
    text-align: center;
    .loading-circle{
      width: 60px;
      height: 60px;
      margin: 0 auto;
      margin-top: 30%;
    }
  }
</style>
<script>
export default {
  props: {
    showLoading: {
      default: false
    }
  },
  data () {
    return {
    };
  },
  created () {
  },
  methods: {
  },
  watch: {
  },
  mounted () {
    this.$nextTick(function () {
    });
  }
};
</script>



