<template>
  <section>
    <globalLoading-component :showLoading="showLoading"></globalLoading-component>
    <div class="header main-header">
      <i class="iconfont icon-jiantouleft left-btn"></i>
      <span>{{headerTitle}}</span>
      <div class="right-btn">
        <i class="iconfont icon-sousuo "></i>
        <i class="iconfont icon-e-mail_icon  ml5"></i>
      </div>
    </div>
    <div class="fc-container">
    <van-pull-refresh class=" m-content" v-model="isLoading" @refresh="onRefresh">
      <!--我的数据-->
      <div class="fc-content">
      <div class="m-info">
        <p class="ml10"  @click="toMyData()">12月累计奖励 <i class="iconfont icon-jiantou1"></i></p>
        <p class="m-info-num"  @click="toMyData()">{{myDataJson.items.total}}<span>元</span></p>
        <van-row gutter="20">
          <van-col span="8">
            <div class="jiangli-tag">
              <p>操作奖励</p>
              <p class="num">{{myDataJson.items.itemList.operReward}}<span>元</span></p>
              <p @click="toRanking()">排名<span class="color-tag green-tag">{{myDataJson.items.itemList.operRanking}}<i class="iconfont icon-arrow-top"></i></span></p>
            </div>
          </van-col>
          <van-col span="8">
            <div class="jiangli-tag text-center">
              <p>航班奖励</p>
              <p class="num text-center">{{myDataJson.items.itemList.flightReward}}<span>元</span></p>
              <p @click="toRanking()">排名 <span class="color-tag red-tag">{{myDataJson.items.itemList.flightRanking}}<i class="iconfont icon-arrow-bottom"></i></span></p>
            </div>
          </van-col>
          <van-col span="8">
            <div class="jiangli-tag text-right">
              <p>运行奖励</p>
              <p class="num text-right">{{myDataJson.items.itemList.runReward}}<span>元</span></p>
              <p @click="toRanking()">排名<span class="color-tag blue-tag">{{myDataJson.items.itemList.runRanking}}</span></p>
            </div>
          </van-col>
        </van-row>
      </div>
      <!--飞行任务-->
      <div class="m-task">
        <van-cell title="飞行任务" class="m-task-title" is-link value="更多" @click="showChooseDate()">
          <template slot="icon">
            <i class="iconfont icon-fj-2 m-task-title-icon"></i>
          </template>
        </van-cell>
        <flightTask></flightTask>
      </div>
      </div>
    </van-pull-refresh>
    </div>
    <van-popup v-model="showDate" position="top">
      <comCalendarMonth ref="calendarChild" :currentYear="currentYear" :currentMonth="currentMonth"
                        :flyDaysList="flyDaysList" :showDate="showDate"></comCalendarMonth>
      <van-row class="button" type="flex" justify="space-around">
        <van-col span="10">
          <van-button type="primary" class="m-btn1" @click="confirmToday()">今天</van-button>
        </van-col>
        <van-col span="10">
          <van-button type="primary" class="m-btn2" @click="confirmDate()">确定</van-button>
        </van-col>
      </van-row>
    </van-popup>
  </section>
</template>
<style scoped lang="scss">
  .fc-container{
    width: 100%;
    position: fixed;
    top: 0px;
    bottom: 0;
    box-sizing: border-box;
    padding-bottom: 60px;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
  }
  .main-header {
    color: #444;
    background: #fff;
    i {
      color: #444;
    }
  }

  .m-content {
    /*width: 100%;*/
    /*height: calc(100% - 88px);*/
    /*overflow-y: auto;*/
    box-sizing: border-box;
    padding-top: 40px;
  }

  .m-info {
    /*position: fixed;*/
    width: 94%;
    margin-left: 3%;
    margin-top: 10px;
    margin-bottom: 10px;
    line-height: 40px;
    font-size: 14px;
    background: #fff;
    border-radius: 5px;
    .m-info-num {
      margin-top: 20px;
      margin-bottom: 20px;
      text-align: center;
      font-family: LangDon;
      font-size: 40px;
      color: #4f5064;
      span {
        font-size: 14px;
        margin-left: 10px;
      }
    }
  }
  .m-task {
    background: #fff;
    font-size: 14px;
    border-bottom: 1px solid #e5e5e5;
    /*height: 150px;*/
    .m-task-title {
      padding: 10px;
      .m-task-title-icon {
        color: #26c88c;
      }
    }
  }
  .jiangli-tag {
    color: #b5b5b5;
    font-size: 14px;
    text-align: left;
    padding-left: 10px;
    padding-right: 10px;
    p {
      margin-bottom: 10px;
      line-height: 20px;
    }
    .num {
      text-align: left;
      font-family: LangDon;
      font-size: 24px;
      color: #505165;
      span {
        font-size: 14px;
        margin-left: 10px;
      }
    }
    .color-tag {
      color: #fff;
      border-radius: 10px;
      margin-left: 5px;
      width: 40px;
      text-align: center;
      display: inline-block;
      font-size: 12px;
      height: 18px;
      line-height: 18px;
      i {
        font-size: 12px;
      }
    }
    .color-tag.green-tag {
      background: #26c88c;
    }

    .color-tag.red-tag {
      background: #ff9600;
    }

    .color-tag.blue-tag {
      background: #7d92d3;
    }
  }

  .button {
    margin: 10px;
    .m-btn1 {
      width: 100%;
      height: 28px;
      line-height: 28px;
      border: 1px solid #26c88c;
      background: #fff;
      border-radius: 14px;
      text-align: center;
      color: #26c88c;
    }
    .m-btn2 {
      width: 100%;
      height: 28px;
      line-height: 28px;
      border: 1px solid #fff;
      background: #26c88c;
      border-radius: 14px;
      text-align: center;
      color: #fff;
    }
  }
</style>
<script>
// import {Toast} from 'vant';
import comCalendarMonth from '../../components/com_Calendar_Month';
import flightTask from './components/flight_task';
import myDataJson from './myData.json';
export default {
  // inject: ['reload'],
  data () {
    return {
      showLoading: false,
      isLoading: false,
      dataList: [],
      keyWord: '',
      selected: '1',
      rangeValue: 30,
      isShowRound: true,
      opacityStyle: {
        opacity: 0
      },
      backStyle: {
        background: 'transparent'
      },
      headerTitle: '飞行',
      currentYear: 2019, // 年份
      currentMonth: 1, // 月份
      currentDay: 1, // 日期
      currentWeek: 1, // 星期
      flyDaysList: [], // 获取的日历信息
      showDate: false,
      myDataJson: myDataJson
    };
  },
  components: {
    flightTask: flightTask,
    comCalendarMonth: comCalendarMonth
  },
  created () {
    // this.loadData();
    // alert(this.$store.getters.getStorage.user.avrId);
    // alert(this.$store.state.home.user.user);
  },

  methods: {
    reloadPage () {
      this.$router.go(0);
    },
    // 显示更多月份选择
    showChooseDate () {
      this.showDate = true;
    },
    // 跳转到我的数据页面
    toMyData () {
      this.$router.push({
        path: '/Main/MyData',
        query: {
          flightDate: this.currentDate

        }
      });
    },
    toRanking () {
      this.$router.push({
        path: '/Mine/myRanking',
        query: {
        }
      });
    },
    onRefresh () {
      setTimeout(() => {
        // this.$toast('刷新成功');
        this.isLoading = false;
        this.loadData();
        // this.count++;
      }, 500);
    },
    confirmToday () {
      this.showDate = false;
    },
    confirmDate () {
      this.showDate = false;
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll);
  },
  unmounted () {
    window.removeEventListener('scroll', this.handleScroll);
  }
};
</script>



