  methods: {
    onClick (index, title) {
      // this.$toast(title);
    },
    loadData () {
      // this.statusItems =  JSON.parse(this.statusItemsStr);
    },
    onRefresh () {
      setTimeout(() => {
        // this.$toast('刷新成功');
        this.isLoading = false;
        this.loadData();
        // this.count++;
      }, 500);
    },

    backGo (to) {
      this.$router.back(-1);
    },
    onLoad () {
      // 异步更新数据
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 500);
    }
  }
};

