<template>
  <section class="main-container">
    <div class="header-base ">
      <!--头部组件-->
      <com_header :headerTitle="headerTitle"></com_header>
      <div class="wrap">
        <index-list :data="playerList" :useLazyLoad="true" @choose="onChoose" v-if="playerList.length"></index-list>
      </div>
    </div>
  </section>

</template>
<style scoped lang="scss">

  * {
    margin: 0;
    padding: 0;
  }

  .wrap {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100%;
  }

  .sa-top {
    position: fixed;
    top: 40px;
    width: 100%;
    background: #4f5064;
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    color: #fff;
    z-index: 999;
    .sa-name {
      padding-left: 10px;
      font-size: 16px;
    }
    .sa-plane {
      position: relative;
      text-align: center;
      .sa-plane-bg {
        position: absolute;
        top: 19px;
        width: 40px;
        height: 1px;
        border-top: 1px dashed #fff;
      }
    }
  }

  .sa-content {
    padding-top: 80px;
    width: 100%;
    min-height: calc(100% - 84px);
    background: #f2f2f2;
  }
</style>
<script>
// import * as indexManageApi from '../../api/main';
import comHeader from '../components/header';
// Vue.use(List);
import IndexList from './components/Com_List';
import playerList from './components/testData.json';

export default {
  data () {
    return {
      headerTitle: '通讯录',
      dataList: [],
      listLoading: false,
      keyWord: '',
      stepList: [],
      flightId: this.$route.query.flightId,
      flight_no: this.$route.query.flight_no,
      arrAirport: this.$route.query.arr_airport,
      deptAirport: this.$route.query.dept_airport,
      aircraftNo: this.$route.query.aircraft_no,
      aircraftType: this.$route.query.aircraft_type,
      list: [],
      loading: false,
      finished: false,
      playerList
    };
  },
  created () {
    this.loadData();
  },
  components: {
    com_header: comHeader,
    IndexList: IndexList
  },
  methods: {
    onClick (index, title) {
      // this.$toast(title);
    },
    onLoad () {
      // 异步更新数据
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 500);
    },
    loadData () {
    },
    onChoose () {

    }
  }
};

</script>



