<template>
  <section>

    <v-touch v-on:swiperight="onSwipeRight" class="touchWapper" :swipe-options="{direction: 'horizontal'}">
    <!--头部组件-->
    <com_header :headerTitle="headerTitle"></com_header>
    <div class="c-container">
      <van-cell-group class="my-info-list">
        <van-cell title="账号安全" size="large" is-link>
          <template slot="title">
            <span  class="h-title">账号安全</span>
          </template>
        </van-cell>
        <van-cell title="消息缓存" size="large">
          <template slot="title">
            <span  class="h-title">消息缓存</span>
          </template>
          <template>
            <span class="h-value">100M</span>
          </template>
        </van-cell>
        <van-cell title="关于" size="large" is-link>
          <template slot="title">
            <span  class="h-title">关于</span>
          </template>
        </van-cell>
      </van-cell-group>
      <van-button type="default" class="login-out" @click="loginOut()">退出登录</van-button>
    </div>
    </v-touch>
  </section>

</template>
<style scoped lang="scss">
  .my-info-list{
    margin-top: 10px;
    .h-title{
      font-size: 14px;
    }
    .h-value{
      color: #0096ff;
      font-size: 14px;
    }
  }
  .login-out{
    width: 90%;
    margin-left: 5%;
    background: #0096ff;
    font-size: 14px;
    color: #fff;
    margin-top: 10px;
  }
</style>
<script>
// 使用jquery
// import * as indexManageApi from '../../api/main';
import comHeader from '../components/header';

export default {
  // inject: ['reload'],
  data () {
    return {
      headerTitle: '设置',
      dataList: []
    };
  },
  components: {
    com_header: comHeader
  },
  created () {
    this.loadData();
  },

  methods: {
    loadData () {
      this.dataList = [
        {id: 1, title: '姓名 ', value: '卓易航'}];
    },
    loginOut () {
      this.$store.commit('$_removeStorage');
      this.$router.push('/login');
    }
  },
  mounted () {
  }
};

</script>



