<template>
  <div class="fuel-report">
  <van-progress class="fuel-report-progress"
                color="#28c88c"
                :percentage="60"
                :show-pivot="false"
  />
  <div class="fuel-report-tagr">
    最大预期节油量500Kg <i class="iconfont icon-xiajiantou1"></i>
