<style lang="scss" scoped>
  .fuel-report{
    padding: 10px;
    position: relative;
    padding-top: 40px;
    padding-bottom: 50px;
    .fuel-report-tag{
      position: absolute;
      right: 30%;
      top: 60px;
      text-align: right;
      line-height: 40px;
      font-size: 14px;
      color: #28c88c;
      background: #fff;
      i{
        color: #666;
      }
    }
    .fuel-report-tagr{
      position: absolute;
      right: 5px;
      top: 0;
      text-align: right;
      line-height: 36px;
      font-size: 14px;
      color: #666;
      background: none;
      i{
        color: #666;
      }
    }
    .fuel-report-progress{
      height: 20px;
      background: #d4f4e8;
    }
  }
</style>

<script>
export default {
  props: {
    baseData: {
      default: []
    }
  },
  ready () {
  },
  data () {
    return {
    };
  },
  methods: {
  }
};
</script>



