<template>
  <div class="main-container">
    <!--<router-view></router-view>-->
      <!--<transition  :name="transitionName" v-touch:left="onSwipeLeft">-->
      <!--<keep-alive>-->
      <!--<router-view class="transitionBody" ></router-view>-->
      <!--</keep-alive>-->
    <!--</transition>-->
    <!--<v-touch v-on:swiperight="onSwipeRight">-->
    <!--<transition :name="$store.state.states">-->
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    <!--</transition>-->
    <!--</v-touch>-->
    <!--<com_footer></com_footer>-->
    <van-tabbar
      v-model="active"
      active-color="#4f5064"
    >
      <van-tabbar-item to="/Main">飞行
        <template slot="icon">
          <i class="iconfont icon-fj-1"></i>
        </template>
      </van-tabbar-item>
      <van-tabbar-item to="/addressBook">通讯录
        <template slot="icon">
          <i class="iconfont icon-tongxunlu2"></i>
        </template></van-tabbar-item>
      <van-tabbar-item  to="/FriendsCircle">节油圈
        <template slot="icon">
          <i class="iconfont icon-quanzi"></i>
        </template></van-tabbar-item>
      <van-tabbar-item  to="/Mine">我
        <template slot="icon">
          <i class="iconfont icon-yonghu1"></i>
        </template></van-tabbar-item>
    </van-tabbar>
  </div>
</template>
<style>

  .transitionBody{
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    transition: all .5s ease;
    /*transition: all 0.15s ease; !*定义动画的时间和过渡效果*!*/
  /*transition: all .5s cubic-bezier(.55,0,.1,1);*/
  }
  .slide-left-enter-active,.slide-right-enter-active {
    transition: all  .2s ease;
  }
  .slide-left-leave-active , .slide-right-leave-active{
    transition: all  .2s ease  ;
  }
  .slide-left-enter,.slide-right-leave-to{
    opacity: 0;
    -webkit-transform: translate(100%, 0);
    transform: translate(100%, 0);
  }
  .slide-right-enter,.slide-left-leave-to{
    opacity: 0;
    -webkit-transform: translate(-100%, 0);
    transform: translate(-100% 0);
  }

  /*.turn-on-enter {*/
    /*transform: translate3d(100%, 0, 0);*/
  /*}*/
  /*.turn-on-leave-to {*/
    /*!* transform: translate3d(-20%, 0, 0); *!*/
  /*}*/
  /*.turn-on-enter-active,*/
  /*.turn-on-leave-active {*/
    /*transition: transform 0.4s ease;*/
  /*}*/
  /*.turn-off-enter {*/
    /*!* transform: translate3d(-20%, 0, 0); *!*/
  /*}*/
  /*.turn-off-leave-to {*/
    /*transform: translate3d(100%, 0, 0);*/
  /*}*/
  /*.turn-off-enter-active,*/
  /*.turn-off-leave-active {*/
    /*transition: transform 0.4s ease;*/
  /*}*/
  /*.turn-off-leave-active {*/
    /*z-index: 2;*/
  /*}*/
</style>
<script>
import '../assets/css/home.scss';
import comFooter from './components/footer';

export default {
  data () {
    return {
      sysName: '数据分析平台',
      transitionName: 'slide-left',
      currentNavId: '',
      allMenuList: [],
      active: 0
    };
  },
  components: {
    'com_footer': comFooter // 计算器组件
  },
  created () {
  },
  methods: {
    // 获取全部菜单和顶部一级菜单列表
    getMenuList () {
      this.allMenuList = this.$user.arrayMenus;
    },
    /**
       *
       * @param navPath 跳转路径
       * @param navItem 二级导航
       * @param subNavItem 三级导航
       */
    openMenu (navPath, navItem, subNavItem) {
      this.$router.push(navPath);
    },
    // 退出登录
    logout: function () {
      this.$router.push('/Login');
      // var _this = this;
      // this.$confirm('确认退出吗?', '提示', {}).then(() => {
      //   sessionStorage.removeItem('user');
      //   sessionStorage.clear();
      //   localStorage.clear();
      // }).catch(() => {
      // }
      // )
      // ;
    }
  },
  mounted () {
  },
  // 监听路由的路径，可以通过不同的路径去选择不同的切换效果
  watch: {
    '$route' (to, from) {
      //    console.log('现在路由:',to.path.split('/')[1],'来自路由:',from.path.split('/')[1],'现在的动画:',this.transitionName)
      // const toDepth = to.path.split('/').length;
      // const fromDepth = from.path.split('/').length;
      // this.transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left';

      if (to.meta.index > 0) {
        if (to.meta.index < from.meta.index) {
          this.transitionName = 'slide-right';
        } else {
          this.transitionName = 'slide-left';
        }
      } else if (to.meta.index === 0 && from.meta.index > 0) {
        this.transitionName = 'slide-right';
      }
    }
  }
};
</script>



