<template>
  <section>
    <v-touch v-on:swiperight="onSwipeRight" class="touchWapper" :swipe-options="{direction: 'horizontal'}" >
    <!--头部组件-->
    <com_header :headerTitle="headerTitle"></com_header>
    <div class="c-container">
      <van-row >
        <van-col span="24" class="h50 border-b1">
          <span class="span-01">更换头像</span>
          <span class="span-02">
            <van-uploader :after-read="onRead" accept="image/gif, image/jpeg" multiple>
              <van-icon class="portrait-icon" :name="filepath" />
            </van-uploader>
          </span>
        </van-col>
        <van-col span="24" class="h50 border-b1">
          <span class="span-01">名称</span>
          <span class="span-02">卓一航</span>
        </van-col>
        <van-col span="24" class="h50 border-b1">
          <span class="span-01">飞行资质</span>
          <span class="span-02">机长</span>
        </van-col>
        <van-col span="24" class="h50 border-b1">
          <span class="span-01">飞行大队</span>
          <span class="span-02">飞行二大队</span>
        </van-col>
      </van-row>
    </div>
    </v-touch>
  </section>

</template>
<style scoped lang="scss">
  .my-info-list{
    margin-top: 10px;
      .h-title{
        font-size: 14px;
      }
  }
  .border-b1 {
    border-bottom: 1px solid #99999945;
    height: 50px;
    line-height: 50px;
  }
  .span-01{
    float: left;
    margin-left: 5%;
    width: 20%;
    font-size: 14px;
    text-align: left;
  }
  .span-02{
    float: right;
    margin-right: 5%;
    width: 50%;
    font-size: 14px;
    text-align: right;
  }
  .portrait-icon{
    width: 40px;
    height: 40px;
  }

</style>
<script>
// 使用jquery
// import * as indexManageApi from '../../api/main';
import comHeader from '../components/header';

export default {
  // inject: ['reload'],
  data () {
    return {
      headerTitle: '个人资料',
      dataList: [],
      filepath: 'https://b.yzcdn.cn/vant/icon-demo-1126.png'
    };
  },
  components: {
    com_header: comHeader
  },
  created () {
    this.loadData();
  },

  methods: {
    loadData () {
      this.dataList = [
        {id: 1, title: '姓名 ', value: '卓易航'}];
    },
    onRead (file) {
      console.log(file);
      this.filepath = file.content;
    }
  },
  mounted () {
  }
};

</script>



