    <div class="sa-content">
      <van-tabs class="rh-tab" v-model="active" swipeable>
        <van-tab v-for="(item,index) in colsList" :title="item.title" :key="index">
          <div class="tab-c" v-if="item.title=='日'">
            <van-row type="flex" justify="space-around"  class="font14">
              <van-col span="6" class="text-center">操作</van-col>
              <van-col span="6" class="text-center">航班</van-col>
              <van-col span="6" class="text-center">运行</van-col>
            </van-row>
            <div class="rh-p">
              <div class="rh-row" v-for="(obj,index) in dayData" :key="index">
                <p>{{obj.sairport}}-{{obj.eairport}}
                  <span class="font-gray">实际：{{obj.stime}} -  {{obj.etime}}</span></p>
                <div class="rh-bar-box" v-if="obj.status==0">
                  <div class="rh-bar">数据生成中

                  </div>
                </div>
                <div class="rh-bar-box" v-else-if="obj.status==2">
                  <div class="rh-bar">未飞行</div>
                </div>
                <div class="rh-bar-box" v-else>
                  <div class="rh-bar  rh-bar-green">758</div>
                </div>
              </div>
            </div>
          </div>
          <div class="tab-c" v-if="item.title=='周'">
            <van-row type="flex" justify="space-around" class="font14">
              <van-col span="6" class="text-center"><i class="blue-block"></i>操作</van-col>
              <van-col span="6" class="text-center"><i class="green-block"></i>航班</van-col>
              <van-col span="6" class="text-center"><i class="orange-block"></i>运行</van-col>
            </van-row>
            <div class="bar-box">
              <com_chart_bar :barChartOption="barChartOption"></com_chart_bar></div>
          </div>
          <div class="tab-c" v-if="item.title=='月'">
            <van-row type="flex" justify="space-around" class="font14">
              <van-col span="6" class="text-center"><i class="blue-block"></i>操作</van-col>
              <van-col span="6" class="text-center"><i class="green-block"></i>航班</van-col>
              <van-col span="6" class="text-center"><i class="orange-block"></i>运行</van-col>
            </van-row>
            <div class="bar-box">
              <com_chart_bar :barChartOption="barChartOption2"></com_chart_bar></div>
          </div>
          <div class="tab-c" v-if="item.title=='总'">
            <van-row type="flex" justify="space-around" class="font14">
              <van-col span="6" class="text-center"><i class="blue-block"></i>操作</van-col>
              <van-col span="6" class="text-center"><i class="green-block"></i>航班</van-col>
              <van-col span="6" class="text-center"><i class="orange-block"></i>运行</van-col>
            </van-row>

            <com_chart_pie :pieChartOption="pieChartOption"></com_chart_pie>
          </div>
          <div class="outer-content">
            <div class="arrow"></div>
            <div class=" m-info">
              <p class="mdb-title" @click="jumpToHistory()">今日航段
                <span class="font-green" v-if="item.title=='日'">2/4</span>
                <span class="font-green" v-else-if="item.title=='月'">48</span>
                <span class="font-green" v-else-if="item.title=='总'">572</span>
                <span class="font-green" v-else>16</span>
                <span class="mdb-title-r">飞行时长 3h 26m </span></p>
              <p class="m-info-num" v-if="item.title=='日'">770<span>元</span></p>
              <p class="m-info-num" v-else-if="item.title=='月'">9635<span>元</span></p>
              <p class="m-info-num" v-else-if="item.title=='总'">25671.5<span>元</span></p>
              <p class="m-info-num" v-else>4852<span>元</span></p>
              <p class="fu-title">今日累计奖励</p>
              <van-row gutter="20">
                <van-col span="8">
                  <div class="jiangli-tag">
                    <p>操作奖励</p>
                    <p class="num">1257<span>元</span></p>
                  </div>
                </van-col>
                <van-col span="8">
                  <div class="jiangli-tag">
                    <p>航班奖励</p>
                    <p class="num">2033<span>元</span></p>
                  </div>
                </van-col>
                <van-col span="8">
                  <div class="jiangli-tag">
                    <p>运行奖励</p>
                    <p class="num">2467<span>元</span></p>
                  </div>
                </van-col>
              </van-row>
            </div>
          </div>
        </van-tab>
        <!--<van-tab title="总">-->
        <!--<div class="tab-c">内容 日</div>-->
        <!--<div class="outer-content">文字文字 总-->
        <!--<div class="arrow"></div>-->
        <!--</div>-->
        <!--</van-tab>-->

      </van-tabs>
    </div>
    </v-touch>
  </section>
</template>
<style scoped lang="scss">

  .sa-content {
    padding-top: 39px;
    width: 100%;
    min-height: calc(100% - 44px);
    background: #f2f2f2;
  }

  .tab-c {
    width: 100%;
    height: 300px;
    background: #4f5064;
    color: #fff;
    padding: 10px;
    box-sizing: border-box;
  }

  .outer-content {
    position: relative;
    width: 100%;
    height: 250px;
    background: #fff;
    padding: 10px;
    box-sizing: border-box;
    .arrow {
      width: 0;
      height: 0;
      border: 8px solid transparent;
      border-bottom-color: #fff;
      position: absolute;
      top: -16px;
      left: 46%;
      content: '';
    }
  }

  .mdb-title {
    position: relative;
    margin-bottom: 5px;
    margin-top: 10px;
    font-size:12px ;
    .mdb-title-r {
      position: absolute;
      right: 0px;
      color: #999;
    }
  }

  .fu-title {
    color: #999;
    text-align: center;
    margin-bottom: 20px;
    font-size: 12px;
  }

  .m-info-num {
    margin-top: 20px;
    margin-bottom: 10px;
    text-align: center;
    font-family: LangDon;
    font-size: 32px;
    color: #4f5064;
    span {
      font-size: 14px;
      margin-left: 10px;
    }
  }

  .jiangli-tag {
    color: #b5b5b5;
    font-size: 14px;
    text-align: center;
    p {
      margin-bottom: 10px;
      line-height: 20px;
    }
    .num {
      text-align: center;
      font-family: LangDon;
      font-size: 24px;
      color: #505165;
      span {
        font-size: 14px;
        margin-left: 10px;
        font-family: 'Microsoft Yahei';
      }
    }
  }

  .rh-row {
    margin-top: 5px;
    margin-bottom: 5px;
    font-size:14px ;
    p {
      line-height: 30px;
      font-size: 14px;
      padding-left: 5px;
      span {
        font-size: 12px;
        color: #999;
      }
    }
    .rh-bar-box {
      .rh-bar {
        width: 100%;
        height: 28px;
        line-height: 28px;
        background: #547471;
        border-radius: 14px;
        text-align: center;
        color: #fff;
        font-size:14px ;
      }
      .rh-bar-green{
        background: #28c88c;
      }
    }
  }
  .blue-block{
    width: 12px;
    height: 12px;
    background: #2ca8fe;
    display: inline-block;
    content: '';
    margin-right: 5px;
  }
  .green-block{
    width: 12px;
    height: 12px;
    background: #3fce98;
    display: inline-block;
    content: '';
    margin-right: 5px;
  }
  .orange-block{
    width: 12px;
    height: 12px;
    background: #ffa930;
    display: inline-block;
    content: '';
    margin-right: 5px;
     }
  .bar-box{
    width: 100%;
    overflow-x: auto;
  }
  .font14{
    font-size: 14px;
  }
</style>
<script>
// import * as indexManageApi from '../../api/main';
import comHeader from '../components/header';
import comChartPie from '../components/charts/chart_pie';
import comChartBar from '../components/charts/chart_bar';

export default {
  data () {
    return {
      headerTitle: '奖励历史',
      hearderColor: 'black',
      colsList: [],
      dayData: [],
      pieChartOption: [],
      barChartOption: [],
      barChartOption2: [],
      active: 1,
      dataList: [],
      listLoading: false,
      keyWord: '',
      stepList: [],
      iType: this.$route.query.iType
      // flightId: this.$route.query.flightId,
      // flight_no: this.$route.query.flight_no,
      // arrAirport: this.$route.query.arr_airport,
      // deptAirport: this.$route.query.dept_airport,
      // aircraftNo: this.$route.query.aircraft_no,
      // aircraftType: this.$route.query.aircraft_type
    };
  },
  created () {
    this.loadColsList();
    this.loadData();
  },
  components: {
    com_header: comHeader,
    'com_chart_pie': comChartPie,
    'com_chart_bar': comChartBar
  },
  methods: {
    onClick (index, title) {
      // this.$toast(title);
    },
    loadData () {
      this.dayData = [
        {
          id: 1,
          sairport: 'ZGSZ',
          eairport: 'ZSSS',
          stime: '08:35',
          etime: '11:30',
          status: 1,
          snum: 758,
          snum2: 436,
          snum3: 125
        },
        {
          id: 2,
          sairport: 'ZSWZ',
          eairport: 'ZBAA',
          stime: '09:22',
          etime: '12:58',
          status: 1,
          snum: 458,
          snum2: 356,
          snum3: 260
        },
        {
          id: 3,
          sairport: 'ZGJN',
          eairport: 'ZSWZ',
          stime: '11:30',
          etime: '15:15',
          status: 0,
          snum: 758,
          snum2: 436,
          snum3: 125
        },
        {
          id: 4,
          sairport: 'ZSSS',
          eairport: 'ZGSG',
          stime: '12:45',
          etime: '16:40',
          status: 2,
          snum: 758,
          snum2: 436,
          snum3: 125
        }];

      this.pieChartOption = {
        chartId: 'pieChart1',
        chartTitle: '',
        chartWidth: '100%',
        itemColor: ['#0b9bfe', '#2ec98f', '#ff9a0a'],
        series_Data: [
          {value: 66000, name: '操作'},
          {value: 66035, name: '航班'},
          {value: 55000, name: '运行'}],
        chartLoading: false
      };
      this.barChartOption = {
        chartId: 'myChart1',
        // chartTitle:'test',
        chartW: '600px',
        itemColor: ['#0195ff'],
        xAxis_Data: ['12.03', '12.10', '12.17', '12.24', '12.31', '1.7', '1.14', '1.21', '1.28', '2.4', '2.11', '2.18', '2.25'],
        series_Data: [15, 20, 26, 14, 18, 20, 16, 12, 20, 12, 18, 22, 17]
      };
      this.barChartOption2 = {
        chartId: 'myChart2',
        // chartTitle:'test',
        chartW: '600px',
        itemColor: ['#0195ff'],
        xAxis_Data: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
        series_Data: [5, 20, 36, 10, 10, 20, 5, 20, 36, 10, 10, 20]
      };
    },
    loadColsList () {
      this.colsList = [
        {id: 1, title: '日'},
        {id: 2, title: '周'},
        {id: 3, title: '月'},
        {id: 4, title: '总'}
      ];
    }
  }
};

</script>



