import apiUtils from '../utils/api';

// 飞行员技术等级
export const getF1igthGrade = {
  p: ['get,/api/v1/app/getF1igthGrade'],
  r: params => {
    return apiUtils.axiosGet(`/api/v1/app/getF1igthGrade` + params, '');
  }
};
// 飞行员资质
export const getPilotQualificat = {
  p: ['get,/api/v1/app/getPilotQualificat'],
  r: params => {
    return apiUtils.axiosGet(`/api/v1/app/getPilotQualificat` + params, '');
  }
};
// 飞行员证照
export const getlicense = {
  p: ['get,/api/v1/app/getlicense'],
  r: params => {
    return apiUtils.axiosGet(`/api/v1/app/getlicense` + params, '');
  }
};
export const isNameRepeat = {
  p: ['post,/api/v1/index/isNameRepeat'],
  r: params => {
    return apiUtils.axiosPost(`/api/v1/index/isNameRepeat`, params);
  }
};



